<template>
  <div class="specific-info">
    <div class="basic-info-title specific-title">
      <span>{{ $t('TAC.workflowDetail.specificInfo') }}</span>
    </div>
    <el-divider class="specific-table-divider"></el-divider>

    <div class="specific-container">
      <specific-info :title="transactionTitle" :infoList="transactionInfoList"></specific-info>
      <table-item
        :tableTitle="accountEntriesTabelTitle"
        :outSideTableData="accountEntriesTabelData"
        :columns="accountEntriesColumn"
      ></table-item>
      <table-item
        :tableTitle="auditLogTabelTitle"
        :tableDesc="auditLogTabelDesc"
        :columns="auditLogColumn"
        :showDivider="false"
      ></table-item>
    </div>
  </div>
</template>

<script>
import SpecificInfo from '@/views/task-center/component/specific-info';
import TableItem from '@/views/task-center/component/table-item.vue';
import { searchTransactionByOrderId, queryDetail } from '@/views/task-center/api/api-task-center';
export default {
  name: 'TransactionConfimation',
  props: {
    orderId: {
      type: String,
      default: ''
    }
  },
  components: {
    SpecificInfo,
    TableItem
  },
  data () {
    return {
      transactionInfoList: [
        {
          label: 'receiptNo',
          key: 'tradeOrderId',
          value: '',
        },
        {
          label: 'status',
          key: 'status',
          value: '',
        },
        {
          label: 'initiationTime',
          key: 'createTime',
          value: '',
        },
        {
          label: 'lastUpdateTime',
          key: 'lastUpdateTime',
          value: '',
        },
        {
          label: 'transactionType',
          key: 'serviceCodeAlias',
          value: '',
        },
        {
          label: 'reasonType',
          key: 'reasonTypeAlias',
          value: '',
        },
        {
          label: 'initiator',
          key: 'initiatorIdentityName',
          value: '',
        },
        {
          label: 'initiatorType',
          key: 'initiatorIdentityType',
          value: '',
        },
        {
          label: 'initiatorGroupCode',
          key: 'initiatorGroupCode',
          value: '',
        },
        {
          label: 'initiatorGroupMemberRole',
          key: 'initiatorGroupMemberRole',
          value: '',
        },
        {
          label: 'creditParty',
          key: 'payeeIdentityName',
          value: '',
        },
        {
          label: 'debitParty',
          key: 'payerIdentityName',
          value: '',
        },
        {
          label: 'requester',
          key: 'initiatorIdentityName',
          value: '',
        },
        {
          label: 'requesterType',
          key: 'initiatorIdentityType',
          value: '',
        },
        {
          label: 'channel',
          key: 'channelName',
          value: '',
        },
        {
          label: 'entryId',
          key: 'entryId',
          value: '',
        },
        {
          label: 'loyaltyPointAmount',
          key: 'loyaltyPointAmount',
          value: '',
        },
        {
          label: 'amount',
          key: 'totalStrAmount',
          value: '',
        },
        {
          label: 'discountAmount',
          key: 'discountStrAmount',
          value: '',
        },
        {
          label: 'remark',
          key: 'remark',
          value: '',
        },
        {
          label: 'commentToCustomer',
          key: 'commentToCustomer',
          value: '',
        },
        {
          label: 'note',
          key: 'note',
          value: '',
        },
        {
          label: 'disputedTransaction',
          key: 'disputedTransaction',
          value: '',
        },
      ],
      accountEntriesColumn: [
        {
          label: this.$t('TAC.specificInfo.identityType'),
          prop: 'identityType',
          align: 'left',
        },
        {
          label: this.$t('TAC.specificInfo.identity'),
          prop: 'identityName',
          align: 'left',
        },
        {
          label: this.$t('TAC.specificInfo.account'),
          prop: 'accountAlias',
          align: 'left',
        },
        {
          label: this.$t('TAC.specificInfo.reasonType'),
          prop: 'reasonTypeAlias',
          align: 'left',
        },
        {
          label: this.$t('TAC.specificInfo.details'),
          prop: 'details',
          align: 'left',
        },
        {
          label: this.$t('TAC.specificInfo.transactionAmount'),
          prop: 'transactionAmount',
          align: 'left',
        },
      ],
      auditLogColumn: [
        {
          label: this.$t('TAC.specificInfo.event'),
          prop: 'event',
          align: 'left',
        },
        {
          label: this.$t('TAC.specificInfo.initiatorType'),
          prop: 'initiatorType',
          align: 'left',
        },
        {
          label: this.$t('TAC.specificInfo.initiator'),
          prop: 'initiator',
          align: 'left',
        },
        {
          label: this.$t('TAC.specificInfo.time'),
          prop: 'time',
          align: 'left',
        },
        {
          label: this.$t('TAC.specificInfo.reason'),
          prop: 'reason',
          align: 'left',
        },
      ],
      accountEntriesTabelData: [],
      transactionTitle: this.$t('TAC.specificInfo.transactionDetails'),
      accountEntriesTabelTitle: this.$t('TAC.specificInfo.accountEntries'),
      auditLogTabelTitle: this.$t('TAC.specificInfo.auditLog'),
      auditLogTabelDesc: this.$t('TAC.specificInfo.auditLogDesc'),
    }
  },
  computed: {},
  watch: {
    orderId(val) {
      if (val) {
        this.queryIdentityId();
      }
    },
  },
  methods: {
    async queryIdentityId() {
      let res = await searchTransactionByOrderId({ tradeOrderId: this.orderId });
      if (res.header && res.header.success) {
        const params = {
          identityId: res.identityId,
          identityType: res.identityType,
          tradeOrderId: res.tradeOrderId,
        }
        this.queryTransactionDetail(params);
      }
    },
    async queryTransactionDetail(params) {
      let res = await queryDetail(params);
      if (res.header && res.header.success) {
        if (!res.basicDetail) return;
        this.transactionInfoList.forEach((item, idx) => {
          this.transactionInfoList[idx].value = res.basicDetail[0][item.key];
        });
        if (!res.entryList) return;
        if (res.entryList.length > 0) {
          this.accountEntriesTabelData.splice(0);
          this.accountEntriesTabelData = res.entryList.map((item) => {
            return {
              identityType: item.identityType,
              identityName: item.identityName,
              accountAlias: item.accountAlias,
              reasonTypeAlias: item.reasonTypeAlias,
              details: item.details,
              transactionAmount: item.transactionAmount,
            };
          });
        }
      }
    },
  },
}
</script>

<style lang='scss' scoped>
@import '../assets/detail.scss';
</style>
