<template>
  <div>
    <div class="basic-info-box">
      <el-row :gutter="20">
        <el-col v-bind="colOptions" v-for="(item, index) of configList" :key="index">
          <div class="basic-info-card">
            <div class="basic-info-card__label bg-purple">{{ item.label }}</div>
            <div class="basic-info-card__value">{{ item.value }}</div>
          </div>
        </el-col>
      </el-row>
    </div>
  </div>
</template>
<script>
export default {
  name: 'BasicInfoBox',

  props: {
    basicInfo: {
      type: Object,
      default: () => ({}),
    },
  },

  data() {
    return {
      propsOrder: [
        {
          name: 'taskId',
          label: this.$t('TAC.taskDetail.taskNo'),
        },
        {
          name: 'createTime',
          label: this.$t('TAC.taskDetail.creationTime'),
        },
        {
          name: 'initiatorNameConcat',
          label: this.$t('TAC.taskDetail.Initiator'),
        },
        {
          name: 'ownerIdConcat',
          label: this.$t('TAC.taskDetail.currentHandler'),
        },
        {
          name: 'processId',
          label: this.$t('TAC.taskDetail.workflowId'),
        },
        {
          name: 'createTime',
          label: this.$t('TAC.taskDetail.workflowStartTime'),
        },
        {
          name: 'procStatus',
          label: this.$t('TAC.taskDetail.workflowStatus'),
        },
        {
          name: 'initiationChannel',
          label: this.$t('TAC.taskDetail.workflowInitiationChannel'),
        },
        {
          name: 'eventSource',
          label: this.$t('TAC.taskDetail.eventSource'),
        },
        {
          name: 'eventSourceSystem',
          label: this.$t('TAC.taskDetail.eventSourceSystem'),
        },
        {
          name: 'workflowReceiver',
          label: this.$t('TAC.taskDetail.workflowReceiver'),
        },
      ],
    };
  },
  computed: {
    configList() {
      const configMap = this.basicInfo;
      return this.propsOrder.map((field) => {
        const name = field.name;
        const label = field.label;
        return {
          label,
          value: configMap[name] || '-',
        };
      });
    },
  },
};
</script>
<style scoped lang="scss">
.basic-info {
  font-weight: bold;
  font-size: 16px;
}
.el-row {
  .basic-info-card {
    padding-bottom: 20px;
    font-size: 14px;

    &__label {
      margin-bottom: 8px;
      color: $--text_color--secondary;
    }

    &__value {
      color: $--text_color--strong;
    }
  }
  .basic-info-card__value {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
}
</style>
