          >
            <div class="operate-concrete-title">{{ $t('TAC.taskDetail.escalate') }}</div>
            <el-divider class="task-divider"></el-divider>
            <el-form-item :label="$t('TAC.taskDetail.Comment')" prop="commentText">
              <el-input
                type="textarea"
                :rows="5"
                resize="none"
                v-model="escalateForm.commentText"
                :placeholder="$t('common.placeholder.enter')"
              />
            </el-form-item>
            <div style="height: 40px">
              <el-button type="primary" size="small" @click="handleConfirm" plain style="float: right"
                >{{ $t('common.button.confirm') }}
              </el-button>
            </div>
          </el-form>

          <el-form
            ref="taskProcessingForm"
            :model="taskProcessingForm"
            :rules="taskProcessingRules"
            size="small"
            label-position="top"
            v-if="showSingleProcess(basicInfoList)"
          >
            <div class="operate-concrete-title">
              {{ $t('TAC.taskDetail.taskProcessing') }}
              <el-divider class="task-process-divider"></el-divider>
            </div>
            <el-form-item :label="$t('TAC.taskDetail.Action')" prop="action">
              <el-radio-group v-model="taskProcessingForm.action" size="small">
                <el-radio v-for="(item, index) in labelAcctions" :key="index" :label="item">
                  {{ item }}
                </el-radio>
              </el-radio-group>
            </el-form-item>

            <el-form-item
              class="processing-operation"
              :label="$t('TAC.taskDetail.Operator')"
              prop="operator"
              v-if="taskProcessingForm.action === 'Investigate' || taskProcessingForm.action === 'Delegate'"
            >
              <el-radio-group
                v-model="taskProcessingForm.operator"
                class="processing-operator"
                size="small"
                v-if="taskProcessingForm.action === 'Investigate'"
              >
                <el-radio
                  v-for="(item, index) in [$t('TAC.taskDetail.Initiator'), $t('TAC.taskDetail.Others')]"
                  :key="index"
                  :label="item"
                >
                  {{ item }}
                </el-radio>
              </el-radio-group>
            </el-form-item>

            <div style="height: 52px" v-if="taskProcessingForm.action === 'Delegate'">
              <div class="multi-select-container">
                <div class="select-tags">
                  <div class="select-scroll">
                    <el-tag
                      type="info"
                      v-for="(item, index) in selectDataList"
                      :key="index"
                      :closable="true"
                      @close="removeSelected(item)"
                    >
                      {{ item.roleName || item.userName }}
                    </el-tag>
                  </div>
                </div>
                <div class="icon-e"><span class="el-icon-ellipsis" @click="dialogVisible = true"></span></div>
              </div>
            </div>

            <div
              style="height: 52px"
              v-if="taskProcessingForm.action === 'Investigate' && taskProcessingForm.operator === 'Others'"
            >
              <div class="multi-select-container">
                <div class="select-tags">
                  <div class="select-scroll">
                    <el-tag
                      type="info"
                      v-for="(item, index) in selectDataList"
                      :key="index"
                      :closable="true"
                      @close="removeSelected(item)"
                    >
                      {{ item.roleName || item.userName }}
                    </el-tag>
                  </div>
                </div>
                <div class="icon-e"><span class="el-icon-ellipsis" @click="openOperatorDialog"></span></div>
              </div>
            </div>
            <el-form-item
              :label="$t('TAC.taskDetail.ValidityPeriod')"
              prop="validityPeriod"
              v-if="taskProcessingForm.action === 'Investigate'"
            >
              <el-radio-group v-model="taskProcessingForm.validityPeriod" size="small">
                <el-radio
                  v-for="(item, index) in [$t('TAC.taskDetail.SameasOriginal'), $t('TAC.taskDetail.Customized')]"
                  :key="index"
                  :label="item"
                >
                  {{ item }}
                </el-radio>
              </el-radio-group>
            </el-form-item>
            <el-row
              v-if="taskProcessingForm.action === 'Investigate' && taskProcessingForm.validityPeriod === 'Customized'"
              :gutter="8"
              class="task-detail-col"
            >
              <el-col :span="4">
                <el-form-item :rules="invesCustomizedDay" prop="customizedDay">
                  <el-input
                    size="small"
                    v-model.trim="taskProcessingTimeForm.customizedDay"
                    :placeholder="$t('TAC.taskDetail.Days')"
                    @input="changeMemberId"
                  ></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="3">{{ $t('TAC.taskDetail.Days') }}</el-col>
              <el-col :span="4">
                <el-form-item :rules="invesCustomizedHours" prop="customizedHours">
                  <el-input
                    size="small"
                    v-model.trim="taskProcessingTimeForm.customizedHours"
                    :placeholder="$t('TAC.taskDetail.Hours')"
                    @input="changeHoures"
                  ></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="3">{{ $t('TAC.taskDetail.Hours') }}</el-col>
              <el-col :span="4">
                <el-form-item :rules="invesCustomizedMinutes" prop="customizedMinutes">
                  <el-input
                    size="small"
                    v-model.trim="taskProcessingTimeForm.customizedMinutes"
                    :placeholder="$t('TAC.taskDetail.Minutes')"
                    @input="changeMinutes"
                  ></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="3">{{ $t('TAC.taskDetail.Minutes') }}</el-col>
            </el-row>

            <el-form-item
              :label="$t('TAC.taskDetail.InitiatorModifyandResubmit')"
              prop="modifyandResubmit"
              v-if="taskProcessingForm.action === 'Reject' && currentAction.includes('backinitiator')"
            >
              <el-switch
                v-model="taskProcessingForm.modifyandResubmit"
                :active-text="switchword"
                :active-value="1"
                :inactive-value="0"
                @change="switchValue"
              ></el-switch>
            </el-form-item>
            <el-form-item
              :label="$t('TAC.taskDetail.resetDueTime')"
              prop="resetDueTime"
              v-if="taskProcessingForm.action === 'Delegate'"
            >
              <el-switch
                v-model="taskProcessingForm.resetDueTime"
                :active-text="switchword"
                :active-value="1"
                :inactive-value="0"
                @change="switchValue"
              ></el-switch>
            </el-form-item>
            <el-form-item :label="$t('TAC.workflowDetail.remark')" prop="remark">
              <el-input
                type="textarea"
                :rows="5"
                resize="none"
                v-model="taskProcessingForm.remark"
                :placeholder="$t('common.placeholder.enter')"
              />
            </el-form-item>
            <div class="operator-foot-btn" style="height: 40px; text-align: right">
              <el-button class="btn-cancel" size="small" @click="handleCancel"
                >{{ $t('common.button.cancel') }}
              </el-button>
              <el-button type="primary" size="small" :disabled="!taskProcessingForm.action" @click="handleSubmit()"
                >{{ $t('common.button.submit') }}
              </el-button>
            </div>
          </el-form>
        </div>
      </div>
      <portal-dialog
        :title="dialogTitle"
        ref="operatorDialogRef"
        :dialogVisible.sync="dialogVisible"
        :searchForm="delegateSearchFrom"
        :formRules="formRules"
        :isOverflowHeight="true"
        :dialogTable="delegateDialogTable"
        :searchLoading.sync="delegateDialogTable.isSearch"
        @close-dialog="delegateCloseDialog"
        @search-reset="delegateDialogReset"
        @cancel-btn-click="delegateCloseDialog"
        @confirm-btn-click="comfirmDelegateHandler"
      >
        <template slot="operation">
          <div class="check-operator">
            <div class="check-label">Select Type</div>
            <el-radio-group @change="checkChange" v-model="checkRadio">
              <el-radio label="operator">Operator</el-radio>
              <el-radio label="role">Role</el-radio>
            </el-radio-group>
          </div>
        </template>
      </portal-dialog>
    </div>
  </div>
</template>

<script>
import {
  queryTaskDetail,
  queryRole,
  kycDetails,
  queryCurrentHandler,
  queryOrgCurrentHandler,
  batchProcess,
  queryOrgOperatorDetail,
} from '@/views/task-center/api/api-task-center';
import BasicInfoItem from '@/views/task-center/component/basic-info-item.vue';
import { inputRequiredValidateObj, selectRequiredValidateObj, len512StrValidateObj } from '@/common/validator/common';
import PortalDialog from '@/views/task-center/component/portal-dialog';

import TaskBulk from './bulk-detail/index.vue';
import TransactionConfimation from './transaction-confimation';
import OrganizationKyc from './organization/detail-org-kyc';
import OrganizationTemp from './organization/detail-org-template.vue';
import OrgOperatorKyc from './org-operator/detail-org-kyc';
import OrgOperatorStatus from './org-operator/detail-org-status';
import CustomerKyc from './customer/detail-customer-kyc';
import InvestigateTask from './investigate';
import OrgOperatorAuthType from './org-operator/detail-org-auth-type';
import CommonShowTitle from './common/detail-common-show-title';
import CommonShowSpecific from './common/detail-common-show-specific';
import { STATUS_MAP, EVENT_SOURCE_MAP } from '@/views/task-center/page/task-detail/utils/data-dic.js';
import { dateFormat } from '@/common/utils/date-time-utils.js';

const DETAIL_COMPONENTS_MAP = {
  investigateTask: 'InvestigateTask',
  approvalBulkPayment: 'TaskBulk',
  approvaBulkTransactionReversal: 'TaskBulk',
  approvalChangeOrgOperatorStatus: 'OrgOperatorStatus',
  approvalChangeOrgOperatorKYC: 'OrgOperatorKyc',
  approvalChangeCustKYC: 'CustomerKyc',
  approvalChangeOrgKYC: 'OrgOperatorAuthType',
  approvalChangeOrgName: 'OrganizationTemp',
  approvalChangeOrgStatus: 'OrganizationTemp',
  approvalChangeOrgProduct: 'OrganizationTemp',
  approvalChangeOrgAcctStatus: 'OrganizationTemp',
  approvalTransactionConfirm: 'TransactionConfimation',
  approvalChangeOrgOperatorChannel: 'OrgOperatorAuthType',
  approvalResetOrgOperatorPIN: 'CommonShowTitle',
  approvalResetOrgOperatorPassword: 'CommonShowTitle',
  approvalCommonTask: 'CommonShowSpecific',
};
export default {
  name: 'taskDetail',
  components: {
    PortalDialog,
    BasicInfoItem,
    TaskBulk,
    TransactionConfimation,
    OrganizationKyc,
    OrganizationTemp,
    OrgOperatorKyc,
    OrgOperatorStatus,
    CustomerKyc,
    InvestigateTask,
    CommonShowSpecific,
    CommonShowTitle,
    OrgOperatorAuthType,
  },
  data() {
    return {
      invesCustomizedDay: [
        {
          validator: this.customizedDayValidator,
          message: 'Enter a value between 0 and 100.',
          trigger: 'change',
        },
      ],
      invesCustomizedHours: [
        {
          validator: this.customizedHoursValidator,
          message: 'Enter a value between 0 and 23.',
          trigger: 'change',
        },
      ],
      invesCustomizedMinutes: [
        {
          validator: this.customizedMinutesValidator,
          message: 'Enter a value between 0 and 59.',
          trigger: 'change',
        },
      ],
      currentAction: [],
      bizContext: {},
      newKycForm: {},
      orderId: '',
      identityId: '',
      variableIdentityId: '',
      showEscalate: false,
      showProcess: true,
      selectRoleType: '',
      labelAcctions: [],
      taskName: '',
      recordCommonList: [],
      receiverIdentityId: '',
      selectDataList: [],
      curNumber: '',
      title: '',
      dialogVisible: false,
      switchword: 'No',
      isResetExpireTime: false,
      escalateForm: {},
      escalateFormRules: {
        commentText: [inputRequiredValidateObj, len512StrValidateObj],
      },
      taskProcessingTimeForm: {
        customizedDay: '',
        customizedHours: '',
        customizedMinutes: '',
      },
      delegateSearchFromData: {
        operatorType: '',
        shortCode: '',
        userName: '',
        firstName: '',
        middleName: '',
        lastName: '',
      },
      formRules: {
        shortCode: [
          {
            required: true,
            message: 'please enter',
            trigger: 'change',
          },
        ],
      },
      shortCodeConfig: {
        label: this.$t('TAC.myTodo.orgShortCode'),
        formItemClass: 'short-code',
        key: 'shortCode',
        prop: 'shortCode',
        comp: 'el-input',
        bind: {
          clearable: true,
          placeholder: this.$t('common.placeholder.enter'),
        },
      },
      delegateDialogTable: {
        columns: [
          {
            label: this.$t('TAC.myTodo.userName'),
            prop: 'userName',
            align: 'left',
          },
          {
            label: this.$t('TAC.myTodo.firstName'),
            prop: 'firstName',
            align: 'left',
          },
          {
            label: this.$t('TAC.myTodo.middleName'),
            prop: 'middleName',
            align: 'left',
          },
          {
            label: this.$t('TAC.myTodo.lastName'),
            prop: 'lastName',
            align: 'left',
          },
        ],
        isSearch: false,
        isCheck: true,
        hasPagination: true,
        paginationConfig: {
          total: 0,
          pageIndex: 1,
          pageSize: 5,
          layout: 'total,prev, pager, next',
        },
        getTableData: this.getDelegateDialogData,
        onSelectionChange: this.delegateDialigSelectChange,
      },
      delegateRoleSearchFormData: {
        roleName: '',
        roleCategory: '',
      },
      tempSelectDataList: [],
      newSearchType: false,
      checkRadio: 'operator',
      delegateRoleSearchFormConfigList: [
        {
          label: this.$t('TAC.myTodo.roleName'),
          key: 'roleName',
          prop: 'roleName',
          comp: 'el-input',
          bind: {
            clearable: true,
            placeholder: this.$t('common.placeholder.enter'),
          },
        },
        {
          label: this.$t('TAC.myTodo.roleCategory'),
          key: 'roleCategory',
          prop: 'roleCategory',
          comp: 'el-select',
          options: [
            {
              value: '',
              label: 'All',
            },
            {
              value: '3000',
              label: 'Organization Operator',
            },
            {
              value: '5000',
              label: 'Organization',
            },
            {
              value: '7000',
              label: 'Group',
            },
            {
              value: '8000',
              label: 'SP',
            },
          ],
          bind: {
            clearable: true,
            placeholder: this.$t('common.placeholder.enter'),
          },
        },
      ],
      tempDelegateTableCol: [],
      basicInfoList: {},
      switchText: 'No',
      addCommentForm: {},
      taskProcessingForm: {},
      taskProcessingRules: {
        remark: [inputRequiredValidateObj],
        action: [selectRequiredValidateObj],
        validityPeriod: [selectRequiredValidateObj],
        operator: [selectRequiredValidateObj],
        modifyandResubmit: [selectRequiredValidateObj],
        resetDueTime: [selectRequiredValidateObj],
      },
      BMEBusiness: '',
    };
  },
  computed: {
    getSpecificComponent() {
      let formUrl = this.$route.query.formURL;
      this.BMEBusiness = formUrl.slice(formUrl.indexOf('=') + 1);
      return DETAIL_COMPONENTS_MAP[this.BMEBusiness];
    },
    showOperate() {
      return /Processing|Paused/.test(this.basicInfoList.taskState);
    },
    taskId() {
      return this.$route.query.taskId || this.$route.query.taskNo;
    },
    processId() {
      return this.basicInfoList.processId;
    },
    status() {
      return this.basicInfoList.taskState;
    },
    delegateSearchFrom() {
      if (this.checkRadio === 'operator') {
        return {
          formConfigList: [
            {
              label: this.$t('TAC.myTodo.operatorType'),
              key: 'operatorType',
              prop: 'operatorType',
              comp: 'el-select',
              options: [
                {
                  value: 'orgOperator',
                  label: 'Organization Operator',
                },
                {
                  value: 'spOperator',
                  label: 'SP Operator',
                },
              ],
              bind: {
                clearable: true,
                placeholder: this.$t('common.placeholder.enter'),
              },
            },
            {
              label: this.$t('TAC.myTodo.userName'),
              key: 'userName',
              prop: 'userName',
              comp: 'el-input',
              bind: {
                clearable: true,
                placeholder: this.$t('common.placeholder.enter'),
              },
            },
            {
              label: this.$t('TAC.myTodo.firstName'),
              key: 'firstName',
              prop: 'firstName',
              comp: 'el-input',
              bind: {
                clearable: true,
                placeholder: this.$t('common.placeholder.enter'),
              },
            },
            {
              label: this.$t('TAC.myTodo.middleName'),
              key: 'middleName',
              prop: 'middleName',
              comp: 'el-input',
              bind: {
                clearable: true,
                placeholder: this.$t('common.placeholder.enter'),
              },
            },
            {
              label: this.$t('TAC.myTodo.lastName'),
              key: 'lastName',
              prop: 'lastName',
              comp: 'el-input',
              bind: {
                clearable: true,
                placeholder: this.$t('common.placeholder.enter'),
              },
            },
          ],
          formData: this.delegateSearchFromData,
        };
      } else {
        return {
          formConfigList: this.delegateRoleSearchFormConfigList,
          formData: this.delegateRoleSearchFormData,
        };
      }
    },
    isAddComment() {
      if (this.addCommentForm && this.addCommentForm.comment) {
        return false;
      } else {
        return true;
      }
    },
    isHistory() {
      return /Rejected|Expired|Completed/.test(this.basicInfoList.taskState);
    },
    isEdit() {
      return this.basicInfoList.taskType === 'Modify' && !this.isHistory;
    },
    dialogTitle() {
      return (
        this.taskProcessingForm.action &&
        this.taskProcessingForm.action.replace(/^[a-z]/, (L) => L.toUpperCase()) + ' Task'
      );
    },
  },
  created() {
    this.init();
  },
  watch: {
    delegateSearchFromData: {
      handler(val) {
        if (val.operatorType === 'orgOperator') {
          if (this.delegateSearchFrom.formConfigList.length === 5) {
            this.newSearchType = true;
            this.delegateSearchFrom.formConfigList.splice(1, 0, this.shortCodeConfig);
          }
        } else {
          if (this.delegateSearchFrom.formConfigList.length === 6) {
            this.newSearchType = false;
            this.delegateSearchFrom.formConfigList.splice(1, 1);
          }
        }
        if (val.shortCode !== '') {
          this.$refs.operatorDialogRef.$refs.dialogSearchForm.clearValidate('shortCode');
        }
      },
      deep: true,
    },
    newSearchType(val) {
      if (val) {
        this.delegateDialogTable.getTableData = this.getOrgDelegateDialogData;
      } else {
        this.delegateDialogTable.getTableData = this.getDelegateDialogData;
      }
    },
  },
  methods: {
    openOperatorDialog() {
      this.delegateSearchFrom.formData.operatorType = 'spOperator';
      this.dialogVisible = true;
    },
    customizedDayValidator(rule, val, callback) {
      let value = this.taskProcessingTimeForm.customizedDay * 1;
      if (value <= 100 && value >= 0) {
        callback();
      } else {
        this.$refs.taskProcessingForm.clearValidate('customizedMinutes');
        this.$refs.taskProcessingForm.clearValidate('customizedHours');
        callback(new Error());
      }
    },
    customizedHoursValidator(rule, val, callback) {
      let value = this.taskProcessingTimeForm.customizedHours * 1;
      if (value <= 23 && value >= 0) {
        callback();
      } else {
        this.$refs.taskProcessingForm.clearValidate('customizedMinutes');
        this.$refs.taskProcessingForm.clearValidate('customizedDay');
        callback(new Error());
      }
    },
    customizedMinutesValidator(rule, val, callback) {
      let value = this.taskProcessingTimeForm.customizedMinutes * 1;
      if (value <= 59 && value >= 0) {
        callback();
      } else {
        this.$refs.taskProcessingForm.clearValidate('customizedHours');
        this.$refs.taskProcessingForm.clearValidate('customizedDay');
        callback(new Error());
      }
    },
    async init() {
      await this.getDetailData();
    },
    showSingleProcess(cardData) {
      return (
        cardData.taskState != 'Paused' &&
        cardData.processFlag == true &&
        this.labelAcctions.length &&
        !this.isInvestigate
      );
    },
    showSingleDelegate(cardData) {
      return cardData.taskState != 'Paused' && cardData.processFlag && cardData.taskType != 'Modify';
    },
    changeMemberId(val) {
      this.taskProcessingTimeForm.customizedDay = val.replace(/[^\d]/g, '');
    },
    changeHoures(val) {
      this.taskProcessingTimeForm.customizedHours = val.replace(/[^\d]/g, '');
    },
    changeMinutes(val) {
      this.taskProcessingTimeForm.customizedMinutes = val.replace(/[^\d]/g, '');
    },
    handle$t(t) {
      return this.$t(t);
    },
    getFutureTime() {
      const DayM = this.taskProcessingTimeForm.customizedDay;
      const HoursM = this.taskProcessingTimeForm.customizedHours;
      const MinutesM = this.taskProcessingTimeForm.customizedMinutes;

      const DayMres = DayM ? Number(DayM) : 0;
      const HoursMres = HoursM ? Number(HoursM) : 0;
      const MinutesMres = MinutesM ? Number(MinutesM) : 0;

      const Minutes = DayMres * 24 * 60 + HoursMres * 60 + MinutesMres;

      const nowData = new Date(); // 1.获取当前日期
      const nowMinutes = nowData.getMinutes(); // 2. 获取当前分钟
      nowData.setMinutes(nowMinutes + Minutes); // 3. 设置当前时间+15分钟：把当前分钟数+15后的值重新设置为date对象的分钟数
      return nowData.toLocaleString().replace(/\//g, '-');
    },
    addComment() {
      const params = {
        processList: [
          {
            processId: this.processId,
            taskId: this.taskId,
          },
        ],
        isBatch: false,
        actionType: 'addcomments',
        comments: this.addCommentForm.comment,
      };
      batchProcess(params).then((res) => {
        if (res.header && res.header.success) {
          this.$modal.msgSuccess({
            message: 'Comment added successfully.',
            type: 'success',
            duration: 3000,
          });
          this.addCommentForm.comment = '';
          this.getDetailData();
        }
      });
    },
    handleConfirm() {
      const paramsObj = {
        processList: [
          {
            processId: this.processId,
            taskId: this.taskId,
          },
        ],
        isBatch: false,
        actionType: 'escalation',
        comments: this.escalateForm.commentText,
      };
      this.$refs.escalateForm.validate((valid) => {
        if (valid) {
          batchProcess(paramsObj).then((res) => {
            if (res.header && res.header.success) {
              this.$modal.msgSuccess({
                message: 'Comment confirmed successfully.',
                type: 'success',
                duration: 3000,
              });
              this.escalateForm.commentText = '';
              this.getDetailData();
            }
          });
        }
      });
    },
    showChannel(val) {
      switch (val) {
        case '1005':
          return 'Web';
        case '1007':
          return 'Handset';
        case '1010':
          return 'API';
        default:
          return;
      }
    },
    showStatus(val) {
      return STATUS_MAP[val] || '-';
    },
    switchTextChange(val) {
      val ? (this.switchText = 'Yes') : (this.switchText = 'No');
    },
    getDelegateDialogData() {
      return new Promise((resolve) => {
        let params = {
          operatorType: 20,
          userName: this.delegateSearchFromData.userName,
          firstName: this.delegateSearchFromData.firstName,
          middleName: this.delegateSearchFromData.middleName,
          lastName: this.delegateSearchFromData.lastName,
          pageNo: this.delegateDialogTable.paginationConfig.pageIndex,
          pageSize: this.delegateDialogTable.paginationConfig.pageSize,
        };
        queryCurrentHandler(params).then((res) => {
          this.selectProcessOptSearchLoading = false;
          this.delegateDialogTable.isSearch = false;
          res.list = res.list.map((item) => {
            item.id = item.identityId;
            return item;
          });
          if (res.header && res.header.success) {
            res.totalRecords = res.total;
            return resolve(res);
          }
        });
      });
    },
    async getOrgDelegateDialogData() {
      let params = {
        shortCode: this.delegateSearchFromData.shortCode,
        operatorName: this.delegateSearchFromData.userName,
        selectType: 'operator',
        operatorType: 30,
        pageSize: this.delegateDialogTable.paginationConfig.pageSize,
        pageNo: this.delegateDialogTable.paginationConfig.pageIndex,
      };
      let res = await queryOrgCurrentHandler(params);
      this.selectProcessOptSearchLoading = false;
      this.delegateDialogTable.isSearch = false;
      if (res.header && res.header.success) {
        let list = res.list.map((item) => {
          item.id = item.operatorId;
          // item.userName = item.operatorName;
          return item;
        });
        return {
          header: res.header,
          list,
          totalRecords: res.totalSize,
        };
      }
    },
    delegateCloseDialog() {
      this.$refs.operatorDialogRef.$refs.dialogSearchForm.resetFields();
      this.dialogVisible = false;
      this.tempSelectDataList.splice(0);
      this.delegateDialogReset();
    },
    delegateDialogReset() {
      this.$refs.operatorDialogRef.$refs.dialogSearchForm.resetFields();
      Object.keys(this.$refs.operatorDialogRef.$refs.dialogSearchForm.formData).forEach((key) => {
        this.$refs.operatorDialogRef.$refs.dialogSearchForm.formData[key] = '';
      });
      this.newSearchType = false;
      this.delegateSearchFrom.formData.operatorType = 'spOperator';
      this.$nextTick(() => {
        this.delegateRoleSearchFormData.roleName = '';
        this.delegateDialogTable.isSearch = true;
      });
    },
    comfirmDelegateHandler() {
      this.selectDataList = this.tempSelectDataList;
      this.dialogVisible = false;
      this.delegateDialogReset();
    },
    delegateDialigSelectChange(val) {
      this.tempSelectDataList = val;
    },
    checkChange(val) {
      this.selectRoleType = val;
      if (val === 'role') {
        this.tempDelegateTableCol = JSON.parse(JSON.stringify(this.delegateDialogTable.columns));
        this.delegateDialogTable.columns = [
          {
            label: this.$t('TAC.myTodo.role'),
            prop: 'roleName',
            align: 'left',
          },
          {
            label: this.$t('TAC.myTodo.description'),
            prop: 'description',
            align: 'left',
          },
          {
            label: this.$t('TAC.myTodo.roleCategory'),
            prop: 'roleCategory',
            align: 'left',
          },
        ];
        this.delegateDialogTable.getTableData = this.queryRoleData;
        this.$nextTick(() => {
          this.delegateDialogTable.isSearch = true;
        });
      } else {
        this.delegateDialogTable.columns = this.tempDelegateTableCol;
        this.delegateDialogTable.getTableData = this.getDelegateDialogData;
        this.delegateDialogReset();
        this.$nextTick(() => {
          this.delegateDialogTable.isSearch = true;
        });
      }
    },
    queryRoleData() {
      let params = {
        selectType: 'role',
        roleName: this.delegateRoleSearchFormData.roleName,
        roleCategory: this.delegateRoleSearchFormData.roleCategory,
        pageNo: this.delegateDialogTable.paginationConfig.pageIndex,
        pageSize: this.delegateDialogTable.paginationConfig.pageSize,
      };
      return new Promise((resolve) => {
        queryRole(params).then((res) => {
          this.selectProcessOptSearchLoading = false;
          this.delegateSearchLoading = false;
          this.delegateDialogTable.isSearch = false;
          if (res.header && res.header.success) {
            let tempData = res.list.map((item) => {
              item.id = item.roleId;
              switch (item.roleType) {
                case '2000':
                  item.roleCategory = 'Sp Operator';
                  break;
                case '3000':
                  item.roleCategory = 'Organization Operator';
                  break;
                case '5000':
                  item.roleCategory = 'Organization';
                  break;
                case '7000':
                  item.roleCategory = 'GROUP';
                  break;
                case '8000':
                  item.roleCategory = 'SP';
                  break;
                default:
                  break;
              }
              return item;
            });
            if (this.delegateRoleSearchFormData.roleCategory) {
              tempData = tempData.filter((item) =>
                item.roleType.includes(this.delegateRoleSearchFormData.roleCategory)
              );
            }
            resolve({
              header: res.header,
              list: tempData.slice(
                (this.delegateDialogTable.paginationConfig.pageIndex - 1) *
                  this.delegateDialogTable.paginationConfig.pageSize,
                this.delegateDialogTable.paginationConfig.pageSize * this.delegateDialogTable.paginationConfig.pageIndex
              ),
              totalRecords: tempData.length,
            });
          }
        });
      });
    },
    async getDetailData() {
      let params = {
        taskId: this.taskId,
      };
      let res = null;
      !/Kyc/.test(this.getSpecificComponent) ? (res = await queryTaskDetail(params)) : (res = await kycDetails(params));
      if (!res) {
        this.basicInfoList = {};
        return;
      }
      this.title = res.taskDTO.taskId;
      this.showEscalate = res.manualEscalate;
      this.taskName = res.taskDTO.taskName;
      this.recordCommonList = res.comments ? res.comments : [];
      const actions = res.taskDTO && res.taskDTO.actions;
      const actionsArr = actions.split(',');
      let labelArr = ['approve', 'complete', 'redo', 'delegate', 'investigate', 'reject'];
      this.currentAction = actionsArr;
      this.labelAcctions = actionsArr
        .filter((element) => labelArr.includes(element))
        .map((el) => this.handle$t(`TAC.taskDetail.${el.charAt(0).toUpperCase() + el.slice(1)}`));
      this.basicInfoList = {
        ...res.taskDTO,
        ...res.processDTO,
      };
      if (this.basicInfoList.taskType == 'Modify') {
        this.labelAcctions = this.labelAcctions.filter((e) => e != 'Delegate');
      }
      this.newKycForm = Object.assign({}, res.identityKYCDataBean);
      const jsonStr = res.bizContext && res.bizContext.variables && res.bizContext.variables.changeInfo;
      if (res.bizContext && res.bizContext.orderId) {
        this.orderId = res.bizContext.orderId;
      }
      this.basicInfoList.initiatorNameConcat =
        (this.basicInfoList.initiatorName || '') + '\\' + (this.basicInfoList.initiatorId || '');
      this.basicInfoList.ownerIdConcat =
        (this.basicInfoList.ownerName || '') + '\\' + (this.basicInfoList.ownerId || '');
      // 当ownerId为0时与sp保持统一的展示逻辑
      if (this.basicInfoList.ownerId == '0') {
        this.basicInfoList.ownerIdConcat = '';
      }
      this.basicInfoList.eventSource = res.bizContext ? EVENT_SOURCE_MAP[res.bizContext.eventSource] : '';
      this.basicInfoList.createTime = dateFormat(
        this.basicInfoList.createTime,
        'DD-MM-YYYY HH:mm:ss',
        'YYYY-MM-DD HH:mm:ss'
      );
      this.basicInfoList.initiationChannel = this.showChannel(this.basicInfoList.channel);
      // 判断changeInfo是否存在
      if (jsonStr) {
        this.bizContext = res.bizContext;
        let newChannel = this.showChannel(res.bizContext.channel);
        // this.basicInfoList.initiationChannel = newChannel;
        let changeInfoObj = JSON.parse(jsonStr);
        this.variableIdentityId = changeInfoObj.identityId || changeInfoObj.receiverIdentityId;
        this.currentTasktype = changeInfoObj.oldStatus;
        this.receiverIdentityId = changeInfoObj.receiverIdentityId;
        let orgResList = await queryOrgOperatorDetail(this.receiverIdentityId);
        if (orgResList.orgOperatorInfo) {
          this.basicInfoList = {
            ...this.basicInfoList,
            workflowReceiver: `${orgResList.orgOperatorInfo.operatorName}/${orgResList.orgOperatorInfo.shortCode}`,
            receiver: `${orgResList.orgOperatorInfo.operatorName}/${orgResList.orgOperatorInfo.shortCode}`,
          };
        }
      }
    },
    switchValue(value) {
      this.curNumber = value;
      if (value == '1') {
        this.switchword = 'Yes';
        this.isResetExpireTime = true;
      } else {
        this.switchword = 'No';
        this.isResetExpireTime = false;
      }
    },
    handleSubmit() {
      let params = {
        processList: [
          {
            processId: this.processId,
            taskId: this.taskId,
          },
        ],
        isBatch: false,
        actionType: this.taskProcessingForm.action,
        comments: this.taskProcessingForm.remark,
      };
      const date =
        60 * 24 * this.taskProcessingTimeForm.customizedDay +
        60 * this.taskProcessingTimeForm.customizedHours +
        1 * this.taskProcessingTimeForm.customizedMinutes;
      let assignee = this.selectDataList.map((item) => {
        if (item.identityId) {
          return {
            operatorId: item.identityId,
            operatorType: this.selectRoleType == 'role' ? '3' : '1',
          };
        }
        if (item.roleId) {
          return {
            roleId: item.roleId,
          };
        }
      });
      if (this.taskProcessingForm.action == this.$t('TAC.taskDetail.Reject')) {
        if (this.curNumber == '1') {
          this.taskProcessingForm.action = 'backinitiator';
          params.actionType = 'backinitiator';
        } else {
          this.taskProcessingForm.action = 'reject';
          params.actionType = 'reject';
        }
      }

      if (this.taskProcessingForm.action == this.$t('TAC.taskDetail.Approve')) {
        this.taskProcessingForm.action = 'approve';
        params.actionType = 'approve';
      }
      if (this.taskProcessingForm.action == this.$t('TAC.taskDetail.Delegate')) {
        this.taskProcessingForm.action = 'delegate';
        params = {
          comments: this.taskProcessingForm.remark,
          actionType: this.taskProcessingForm.action,
          assigneeType: this.selectRoleType == 'role' ? '3' : '1',
          assignee: assignee,
          processList: [
            {
              processId: this.processId,
              taskId: this.taskId,
            },
          ],
          isBatch: false,
          isResetExpireTime: this.isResetExpireTime,
        };
      }

      if (this.taskProcessingForm.action == this.$t('TAC.taskDetail.Investigate')) {
        const actionType = 'investigate';
        if (this.taskProcessingForm.operator == this.$t('TAC.taskDetail.Initiator')) {
          if (this.taskProcessingForm.validityPeriod == this.$t('TAC.taskDetail.SameasOriginal')) {
            params = {
              processList: [
                {
                  processId: this.processId,
                  taskId: this.taskId,
                },
              ],
              isBatch: false,
              actionType: actionType,
              comments: this.taskProcessingForm.remark,
              isResetExpireTime: false,
            };
          }
          if (this.taskProcessingForm.validityPeriod == this.$t('TAC.taskDetail.Customized')) {
            params = {
              processList: [
                {
                  processId: this.processId,
                  taskId: this.taskId,
                },
              ],
              isBatch: false,
              actionType: actionType,
              comments: this.taskProcessingForm.remark,
              taskExpireTimeInterval: date,
              isResetExpireTime: true,
            };
          }
        }
        if (this.taskProcessingForm.operator == this.$t('TAC.taskDetail.Others')) {
          if (this.taskProcessingForm.validityPeriod == this.$t('TAC.taskDetail.SameasOriginal')) {
            params = {
              processList: [
                {
                  processId: this.processId,
                  taskId: this.taskId,
                },
              ],
              isBatch: false,
              assigneeType: this.selectRoleType == 'role' ? '3' : '1',
              actionType: actionType,
              comments: this.taskProcessingForm.remark,
              assignee: assignee,
              isResetExpireTime: false,
            };
          }
          if (this.taskProcessingForm.validityPeriod == this.$t('TAC.taskDetail.Customized')) {
            params = {
              processList: [
                {
                  processId: this.processId,
                  taskId: this.taskId,
                },
              ],
              isBatch: false,
              assigneeType: this.selectRoleType == 'role' ? '3' : '1',
              actionType: actionType,
              assignee: assignee,
              comments: this.taskProcessingForm.remark,
              taskExpireTimeInterval: date,
              isResetExpireTime: true,
            };
          }
        }
      }
      this.taskProcessingForm.operator = {};
      this.$refs['taskProcessingForm'].validate((valid) => {
        if (valid) {
          batchProcess(params).then((res) => {
            if (res.header && res.header.success) {
              this.$modal.msgSuccess({
                message: 'Task processed successfully',
                type: 'success',
                duration: 3000,
              });
              this.getDetailData();
              this.taskProcessingForm.remark = '';
              this.$store.dispatch('tagsView/delView', this.$route);
              this.$router.push('list');
            }
          });
        }
      });
    },
    removeSelected(record) {
      this.selectDataList.splice(this.selectDataList.indexOf(record), 1);
    },

    handleCancel() {
      this.taskProcessingForm.remark = '';
    },
  },
};
</script>

