<template>
  <portal-dialog
    :visible.sync="dialogVisible"
    :width="'448px'"
    @closed="cancelFn"
    @cancel-btn-click="cancelFn"
    @confirm-btn-click="confirmFn"
    :title="title"
    :submitLoading="submitLoading"
  >
    <template #content>
      <el-form label-position="top" ref="formRef" :model="formData" :rules="formRules">
        <el-form-item
          prop="currentAuthenticationType"
          :label="$t('IDC.organizationOperatorDetail.label.currentAthType')"
        >
          <div>{{ specificInfo.changeAccessChannel }}</div>
        </el-form-item>
        <el-form-item prop="authenticationType" :label="$t('IDC.organizationOperatorDetail.label.authenticationType')">
          <el-checkbox-group v-model="formData.authenticationType" :min="1">
            <el-checkbox label="1005" key="1005" id="1005">{{
              $t('IDC.organizationOperatorDetail.label.web')
            }}</el-checkbox>
            <el-checkbox label="1007" key="1007" id="1007">
              {{ $t('IDC.organizationOperatorDetail.label.handset') }}</el-checkbox
            >
          </el-checkbox-group>
        </el-form-item>
        <el-form-item
          prop="operatorName"
          :label="$t('IDC.organizationOperatorDetail.label.UserName')"
          v-if="formData.authenticationType && formData.authenticationType.includes('1005')"
        >
          <el-input
            v-model="formData.operatorName"
            :disabled="specificInfo.operatorName ? true : false"
            :placeholder="$t('common.placeholder.enter')"
          >
          </el-input>
        </el-form-item>
        <el-form-item
          prop="operatorId"
          :label="$t('IDC.organizationOperatorDetail.label.OperatorID')"
          v-if="formData.authenticationType && formData.authenticationType.includes('1007')"
        >
          <el-input
            v-model="formData.operatorId"
            :disabled="specificInfo.operatorId ? true : false"
            :placeholder="$t('common.placeholder.enter')"
          >
          </el-input>
        </el-form-item>
        <el-form-item :label="$t('IDC.organizationOperatorDetail.label.Remark')" prop="remark">
          <el-input v-model="formData.remark" type="textarea" :maxlength="512" />
        </el-form-item>
      </el-form>
    </template>
  </portal-dialog>
</template>

<script>
import PortalDialog from '@/components/portal-dialog';
import { inputRequiredValidateObj, selectRequiredValidateObj } from '@/common/validator/common';
export default {
  components: { PortalDialog },
  props: {
    title: {
      type: String,
      default: null,
    },
    specificInfo: {
      type: Object,
      default: () => {},
    },
  },
  data() {
    return {
      dialogVisible: false,
      formData: {
        authenticationType: [],
        operatorName: '',
        operatorId: '',
        remark: null,
      },
      formRules: {
        authenticationType: [selectRequiredValidateObj],
        operatorName: [inputRequiredValidateObj],
        operatorId: [inputRequiredValidateObj],
        remark: [inputRequiredValidateObj],
      },
      submitLoading: false,
    };
  },
  methods: {
    showDialog() {
      this.formData.operatorName = this.specificInfo.operatorName
        ? this.specificInfo.operatorName
        : this.specificInfo.changeOperatorName;
      this.formData.operatorId = this.specificInfo.operatorId
        ? this.specificInfo.operatorId
        : this.specificInfo.changeOperatorId;
      if (this.specificInfo.newAccessChannelArr) {
        this.formData.authenticationType = this.specificInfo.newAccessChannelArr;
      } else {
        this.formData.authenticationType = [];
      }
      this.dialogVisible = true;
      this.$nextTick(() => {
        this.$refs.formRef.resetFields();
      });
    },
    cancelFn() {
      this.dialogVisible = false;
      this.submitLoading = false;
    },
    confirmFn() {
      this.$refs.formRef.validate((valid) => {
        if (valid) {
          this.submitLoading = true;
          this.$emit('submitInfo', this.formData);
        }
      });
    },
  },
};
</script>

<style lang="scss" scoped></style>
