<template>
  <div class="specific-info">
    <div class="basic-info-title specific-title">
      <span>{{ $t('TAC.workflowDetail.specificInfo') }}</span>
    </div>
    <el-divider class="specific-table-divider"></el-divider>
    <div class="specific-content">
      <div class="specific-content-title">{{ $t('TAC.workflowDetail.basicInfo') }}</div>
      <basic-info-tow :basicInfoData="specificDetailData"></basic-info-tow>
      <basic-info-box :basicInfoItem="specificBasicInfoList"></basic-info-box>
      <el-divider class="table-divider change-info-divider"></el-divider>
      <div class="specific-content-title change-info-title">
        {{ $t('TAC.workflowDetail.changeInfo') }}
      </div>
      <basic-info-item-box
        :datalist="taskDetail"
        :basicInfoItemList="specificChangeInfoList"
        @show-change-status="showDialog"
      ></basic-info-item-box>
    </div>

    <org-edit-status
      :editStatusVisible.sync="statusDialogVisible"
      :curStatus.sync="oldStatus"
      :editForm="editForm"
      :receiverIdentityId.sync="receiverIdentityId"
      :datalist="taskDetail"
      :currentTasktype="currentTasktype"
      :bizContext="bizContext"
      @closeDialog="closeDialog"
      @change-status-succeed="changeStatusSucceed"
    ></org-edit-status>
  </div>
</template>

<script>
import BasicInfoTow from '@/views/task-center/component/basic-info-tow.vue';
import BasicInfoBox from '@/views/task-center/component/basic-info-box.vue';
import BasicInfoItemBox from '@/views/task-center/component/basic-info-item-box.vue';
import OrgEditStatus from './edit-status.vue';
import { queryOrgOperatorDetail } from '@/views/task-center/api/api-task-center';
import { dateFormat } from '@/common/utils/date-time-utils';
export default {
  name: 'OrgOperatorStatus',
  props: {
    specificDetailData: {
      type: Object,
      default: () => {},
    },
    receiverIdentityId: {
      type: String,
      default: '',
    },
    bizContext: {
      type: Object,
      default: () => {},
    },
    taskDetail: {
      type: Object,
      default: () => {},
    },
    isEdit: {
      type: Boolean,
      default: false,
    },
  },
  components: {
    BasicInfoTow,
    BasicInfoBox,
    BasicInfoItemBox,
    OrgEditStatus,
  },
  data() {
    return {
      statusDialogVisible: false,
      specificBasicInfoList: {},
      specificChangeInfoList: {},
      oldStatus: '',
      editForm: {
        currentStatus: '',
        reason: '',
        remark: '',
      },
      currentTasktype: '',
    };
  },
  computed: {},
  watch: {
    receiverIdentityId(val) {
      this.getOrgOperatorDetail(val);
    },
  },
  methods: {
    showStatus(val) {
      switch (val) {
        case '00':
          return 'New';
        case '02':
          return 'Pending Active';
        case '03':
          return 'Active';
        case '04':
          return 'Suspended';
        case '05':
          return 'Frozen';
        case '06':
          return 'Closed';
        case '07':
          return 'Capped';
        case '08':
          return 'Dormant';
        default:
          return;
      }
    },
    async getOrgOperatorDetail(receiverIdentityId) {
      let orgResList = await queryOrgOperatorDetail(receiverIdentityId);
      orgResList.orgOperatorInfo.accessChannel.forEach((item, idx) => {
        switch (item) {
          case '1005':
            orgResList.orgOperatorInfo.accessChannel[idx] = 'Web';
            break;
          case '1007':
            orgResList.orgOperatorInfo.accessChannel[idx] = 'Handset';
            break;
          case '1010':
            orgResList.orgOperatorInfo.accessChannel[idx] = 'API';
            break;
          default:
            return;
        }
      });
      this.specificBasicInfoList = orgResList.orgOperatorInfo;
      this.specificBasicInfoList.status = this.showStatus(orgResList.orgOperatorInfo.status);
      this.specificBasicInfoList.accessChannel = orgResList.orgOperatorInfo.accessChannel.join();
      let createTime = orgResList.orgOperatorInfo.createTime.map((item) => {
        if (item < 10) {
          return (item = '0' + item);
        } else {
          return item;
        }
      });
      let curTime = createTime.join().replace(/,/g, '');
      this.specificBasicInfoList.createTime = dateFormat(curTime, 'DD/MM/YYYY HH:mm:ss', 'YYYYMMDDHHmmss');
      const changeInfoObj = JSON.parse(this.bizContext.variables.changeInfo);
      this.specificChangeInfoList = {
        oldStatus: this.showStatus(changeInfoObj.oldStatus),
        newStatus: this.showStatus(changeInfoObj.newStatus),
        reason: changeInfoObj.remark,
        remark: changeInfoObj.remark,
        taskType: this.taskDetail.taskType,
      };
    },
    showDialog() {
      this.statusDialogVisible = true;
    },
    closeDialog() {
      this.statusDialogVisible = false;
    },
    changeStatusSucceed() {
      this.$message.success('Submit ' + this.$t('TAC.tips.successFully'));
      setTimeout(() => {
        window.close();
      }, 500);
    },
  },
};
</script>

<style lang="scss" scoped>
@import '../assets/detail.scss';
/deep/ .change-info-divider {
  margin: 4px 0 1px 0;
  background-color: #fafafa !important;
  box-shadow: 0px -1px 0px #f0f0f0 inset;
}
.change-info-title {
  padding-top: 19px;
}
</style>
