<template>
  <div class="specific-info">
    <div class="basic-info-title specific-title">
      <span>{{ $t('TAC.workflowDetail.specificInfo') }}</span>
    </div>
    <el-divider class="specific-table-divider"></el-divider>
    <div class="specific-container">
      <div class="specific-content">
        <div class="specific-content-title">
          <span>{{ $t('TAC.workflowDetail.basicInfo') }}</span>
        </div>
      </div>
    </div>
    <specific-info
      class="specific-content-custom"
      :infoList="investigateSpecificInfoList"
      @item-click="toParrentDetail"
    ></specific-info>
  </div>
</template>

<script>
import SpecificInfo from '@/views/task-center/component/specific-info';
export default {
  name: 'InvestigateTask',
  props: {
    taskDetail: {
      type: Object,
      default: () => {},
    },
  },
  components: {
    SpecificInfo,
  },
  data() {
    return {};
  },
  methods: {
    toParrentDetail() {
      const queryParams = {
        isInvestigate: true,
        taskState: this.taskDetail.taskState,
        taskId: this.taskDetail.parentTaskId,
        formURL: this.taskDetail.parentFormURL,
      };
      this.openNewWindow({ path: 'detail', query: queryParams });
    },
  },
  computed: {
    investigateSpecificInfoList() {
      return [
        {
          label: 'originalTask',
          value: this.taskDetail.parentTaskId,
          isClick: true,
          col: 1,
        },
        {
          label: 'taskDescription',
          value: this.taskDetail.taskDesc,
          col: 1,
        },
      ];
    },
  },
};
</script>

<style lang="scss" scoped>
@import '../assets/detail.scss';
.specific-info {
  ::v-deep {
    .specific-content-custom {
      padding-top: 0;
    }
  }
}
</style>
