<template>
  <div class="specific-info">
    <div class="basic-info-title specific-title">
      <span>{{ $t('TAC.workflowDetail.specificInfo') }}</span>
    </div>
    <el-divider class="specific-table-divider"></el-divider>
    <div class="specific-content">
      <div class="specific-content-title">{{ $t('TAC.workflowDetail.basicInfo') }}</div>
      <basic-info-box :basicInfoItem="specificBasicInfoList"></basic-info-box>
      <el-divider class="table-divider change-info-divider"></el-divider>
    </div>
    <div class="specific-info">
      <specific-info :title="$t('TAC.workflowDetail.changeInfo')" :infoList="specificChangeInfoList">
        <template slot="new-auth-type">
          <el-link v-if="isEdit" :underline="false" icon="el-icon-edit" @click="handleShowEdit">
            {{ specificChangeInfoList[3].value || '-' }}
          </el-link>
          <span v-else>{{ specificChangeInfoList[3].value || '-' }}</span>
        </template>
      </specific-info>
    </div>
    <edit-auth-type-dialog
      ref="editAuthTypeDialog"
      :title="$t('IDC.organizationOperatorDetail.label.editAthType')"
      :specificInfo="specificValMap"
      @submitInfo="submitTaskProgressInfo"
    ></edit-auth-type-dialog>
  </div>
</template>

<script>
import BasicInfoBox from '@/views/task-center/component/basic-info-box.vue';
import SpecificInfo from '@/views/task-center/component/specific-info';
import { orgOperatorDetail } from '@/views/identifier-center/api/api-organization-operator.js';
import { dateFormat } from '@/common/utils/date-time-utils';
import { CHANNEL_MAP } from '@/views/task-center/page/task-detail/utils/data-dic.js';
import EditAuthTypeDialog from '@/views/task-center/page/task-detail/org-operator/components/edit-auth-type-dialog.vue';
import { queryBackToInitiator } from '@/views/task-center/api/api-task-center';
import { cloneDeep } from 'lodash';
export default {
  name: 'OrgOperatorStatus',
  components: {
    BasicInfoBox,
    SpecificInfo,
    EditAuthTypeDialog,
  },
  props: {
    specificDetailData: {
      type: Object,
      default: () => {},
    },
    receiverIdentityId: {
      type: String,
      default: '',
    },
    bizContext: {
      type: Object,
      default: () => {},
    },
    taskDetail: {
      type: Object,
      default: () => {},
    },
    isEdit: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      specificValMap: {},
      specificBasicInfoList: {},
      specificChangeInfoList: [
        {
          label: 'oldAuthType',
          value: null,
        },
        {
          label: 'userName',
          value: null,
        },
        {
          label: 'operatorId',
          value: null,
        },
        {
          label: 'newAuthType',
          value: null,
          slot: 'new-auth-type',
        },
        {
          label: 'userName',
          value: null,
        },
        {
          label: 'operatorId',
          value: null,
        },
        {
          label: 'remark',
          value: this.taskDetail.reason,
          col: 24,
        },
      ],
    };
  },
  computed: {},
  watch: {
    receiverIdentityId(val) {
      this.getOrgOperatorDetail(val);
    },
    bizContext(val) {
      if (val) {
        let changeData = JSON.parse(val.variables.changeInfo);
        this.specificChangeInfoList[3].value = this.getAccessChannelName(changeData.accessChannel);
        this.specificChangeInfoList[4].value = changeData.operatorName;
        this.specificChangeInfoList[5].value = changeData.operatorId;
        this.specificChangeInfoList[6].value = changeData.reason ? changeData.reason : changeData.remark;
        this.specificValMap.changeAccessChannel = this.specificChangeInfoList[3].value;
        this.specificValMap.changeOperatorName = changeData.operatorName;
        this.specificValMap.changeOperatorId = changeData.operatorId;
        this.specificValMap.newAccessChannelArr = this.getAccessChannelArr(changeData.accessChannel);
      }
    },
  },
  methods: {
    showStatus(val) {
      switch (val) {
        case '00':
          return 'New';
        case '02':
          return 'Pending Active';
        case '03':
          return 'Active';
        case '04':
          return 'Suspended';
        case '05':
          return 'Frozen';
        case '06':
          return 'Closed';
        case '07':
          return 'Capped';
        case '08':
          return 'Dormant';
        default:
          return;
      }
    },
    async getOrgOperatorDetail(receiverIdentityId) {
      let orgResList = await orgOperatorDetail({ receiverIdentityId });
      this.specificBasicInfoList = orgResList.orgOperatorInfo;
      this.specificBasicInfoList.status = this.showStatus(orgResList.orgOperatorInfo.status);
      this.specificBasicInfoList.accessChannel = this.getAccessChannelName(orgResList.orgOperatorInfo.accessChannel);
      let createTime = orgResList.orgOperatorInfo.createTime.map((item) => {
        if (item < 10) {
          return (item = '0' + item);
        } else {
          return item;
        }
      });
      let curTime = createTime.join().replace(/,/g, '');
      this.specificBasicInfoList.createTime = dateFormat(curTime, 'DD/MM/YYYY HH:mm:ss', 'YYYYMMDDHHmmss');
      this.specificChangeInfoList[0].value = this.specificBasicInfoList.accessChannel;
      this.specificChangeInfoList[1].value = this.specificBasicInfoList.accessChannel.includes('Web')
        ? orgResList.orgOperatorInfo.operatorName
        : '-';
      this.specificChangeInfoList[2].value = this.specificBasicInfoList.accessChannel.includes('Handset')
        ? orgResList.orgOperatorInfo.operatorId
        : '-';
      this.specificValMap.operatorId = orgResList.orgOperatorInfo.operatorId;
      this.specificValMap.operatorName = orgResList.orgOperatorInfo.operatorName;
    },
    getAccessChannelName(accessChannel) {
      if (Array.isArray(accessChannel)) {
        return accessChannel.map((item) => CHANNEL_MAP[item]).join(',');
      }
      if (accessChannel && accessChannel.includes(',')) {
        let arr = accessChannel.split(',');
        return arr.map((item) => CHANNEL_MAP[item]).join(',');
      }
      return CHANNEL_MAP[accessChannel] || '-';
    },
    getAccessChannelArr(accessChannel) {
      if (Array.isArray(accessChannel)) {
        return accessChannel;
      }
      if (accessChannel) {
        return accessChannel.split(',');
      }
      return [];
    },
    handleShowEdit() {
      this.$refs.editAuthTypeDialog.showDialog();
    },
    submitTaskProgressInfo(data) {
      const bizContext = cloneDeep(this.bizContext);
      bizContext.variables.changeInfo = JSON.parse(bizContext.variables.changeInfo);
      this.$refs.editAuthTypeDialog.submitLoading = false;
      if (data.authenticationType.includes('1005')) {
        bizContext.variables.changeInfo.operatorName = data.operatorName;
      } else {
        bizContext.variables.changeInfo.operatorName = '';
      }
      bizContext.variables.changeInfo.accessChannel = data.authenticationType.join(',');
      if (data.authenticationType.includes('1007')) {
        bizContext.variables.changeInfo.operatorId = data.operatorId;
      } else {
        bizContext.variables.changeInfo.operatorId = '';
      }
      bizContext.variables.changeInfo = JSON.stringify(bizContext.variables.changeInfo);
      const params = {
        actionType: 'Approve',
        initiatorId: this.taskDetail.initiatorId,
        processId: this.taskDetail.processId,
        taskId: this.taskDetail.taskId,
        comments: data.remark,
        bizContext,
      };
      queryBackToInitiator(params)
        .then((res) => {
          if (res.header && res.header.success) {
            this.$message.success('Submit ' + this.$t('TAC.tips.taskSuccess'));
            this.$refs.editAuthTypeDialog.cancelFn();
            this.$router.push('list');
          } else {
            this.$message.error('Submit ' + this.$t('TAC.tips.taskFailed'));
          }
        })
        .finally(() => {
          this.$refs.editAuthTypeDialog.submitLoading = false;
        });
    },
  },
  mounted() {},
};
</script>

