<template>
  <div class="specific-info">
    <div class="basic-info-title specific-title">
      <span>{{ taskDetail.taskDesc }}</span>
    </div>
    <div class="specific-container">
      <specific-info :infoList="investigateSpecificInfoList"></specific-info>
    </div>
  </div>
</template>

<script>
import SpecificInfo from '@/views/task-center/component/specific-info';
export default {
  name: 'InvestigateTask',
  props: {
    taskDetail: {
      type: Object,
      default: () => {},
    },
    bizContext: {
      type: Object,
      default: () => {},
    },
  },
  components: {
    SpecificInfo,
  },
  data() {
    return {
      investigateSpecificInfoList: [
        {
          label: 'remark',
          value: null,
          col: 24,
        },
      ],
    };
  },
  watch: {
    bizContext(val) {
      if (val) {
        this.investigateSpecificInfoList[0].value = JSON.parse(val.variables.changeInfo).remark;
      }
    },
  },
  methods: {},
};
</script>

