<template>
  <div class="specific-info">
    <div class="basic-info-title specific-title">
      <span>{{ $t('TAC.workflowDetail.specificInfo') }}</span>
    </div>
    <el-divider class="specific-table-divider"></el-divider>
    <div class="specific-container">
      <specific-info :title="operatorTitle" :infoList="operatorSpecificInfoList"></specific-info>
    </div>
    <div class="task-bulk-detail">
      <div class="task-bulk-detail-content">
        <div class="basic-info">
          <basic-info-items
            :infoList="specificDetailData"
            @item-click="handleClick"
            @item-down-load="handleDownLoad"
          ></basic-info-items>
        </div>
      </div>
      <portal-dialog
        :visible.sync="dialogVisible"
        :title="dialogTitle"
        :cancelText="$t('common.button.close')"
        width="800px"
        :height="'442px'"
        top="12vh"
        :before-close="handleDialogCancel"
        @cancel-btn-click="handleDialogCancel"
        :showSubmit="false"
      >
        <template v-slot:content>
          <table-page
            size="small"
            :has-pagination="true"
            :is-search="isSearch"
            :initializingLoading="true"
            :columns="dialogColumns"
            :pagination="paginationConfig"
            :getTableData="getTableData"
          >
          </table-page>
        </template>
      </portal-dialog>
    </div>
  </div>
</template>
<script>
import BasicInfoItems from '@/views/task-center/component/specific-info/index.vue';
import { queryBulkSpecificInfo, queryValidRecords } from '@/views/task-center/api/api-task-center.js';
import { formatDateTimeStr3 } from '@/common/utils/date-time-utils';
import PortalDialog from '@/components/portal-dialog/index.vue';
import tablePage from '@/components/tablePage/index.vue';
import { downloadBulkFile } from '@/views/business-center/pages/bulk/api/api-bulk';
export default {
  name: 'taskBulk',
  components: { BasicInfoItems, PortalDialog, tablePage },
  props: {
    bizContext: {
      type: Object,
      default: () => {},
    },
    routerType: {
      type: String,
      default: '',
    },
  },
  data() {
    return {
      specificDetail: {},
      dialogVisible: false,
      dialogTitle: this.$t('TAC.specificInfo.dialogVisible'),
      paginationConfig: {
        pageIndex: 1,
        pageSize: 10,
        layout: 'total, prev, pager, next',
      },
      dialogColumns: [
        { label: `Number`, prop: 'lineNo', align: 'left' },
        { label: `Summary`, prop: 'summary', align: 'left' },
        { label: `Excution Time`, prop: 'excutionTime', align: 'left' },
        { label: `Error Message`, prop: 'errorDesc', align: 'left' },
        { label: `Cause`, prop: 'errorCause', align: 'left' },
        { label: `Solution`, prop: 'errorSolution', align: 'left' },
      ],
      isSearch: false,
    };
  },
  watch: {
    bulkPlanID: {
      handler(val) {
        if (val != '') {
          this.getDetailData();
        }
      },
    },
  },
  computed: {
    specificDetailData() {
      return [
        {
          label: 'bulkType',
          value: this.specificDetail.bulkTypeName || '-',
        },
        {
          label: 'bulkPlanID',
          value: this.specificDetail.planID || '-',
        },
        {
          label: 'planName',
          value: this.specificDetail.planName || '-',
        },
        {
          label: 'status',
          value: this.specificDetail.bulkStatus || '-',
        },
        {
          label: 'createdBy',
          value: this.specificDetail.createdBy || '-',
        },
        {
          label: 'creationTime',
          value: formatDateTimeStr3(this.specificDetail.createTime) || '-',
        },
        {
          label: 'scheduleTime',
          value: formatDateTimeStr3(this.specificDetail.scheduleTime) || '-',
        },
        {
          label: 'dataFile',
          value: this.specificDetail.dataFileName || '-',
          isDownLoad: true,
        },
        {
          label: 'totalUploadedItems',
          value: this.specificDetail.numberOfTotal || '-',
        },
        {
          label: 'validRecords',
          value: this.specificDetail.numberOfValidSuccess || '-',
        },
        {
          label: 'validAmount',
          value: this.specificDetail.validAmount || '-',
        },
        {
          label: 'failedInitialValidation',
          value: this.specificDetail.numberOfValidFailed || '-',
          isClick: true,
        },
      ];
    },
    bulkPlanID() {
      if (this.bizContext && this.bizContext.variables.bulkPlanID) {
        return this.bizContext.variables.bulkPlanID;
      } else {
        return '';
      }
    },
  },
  methods: {
    async getTableData() {
      let params = {
        offset: (this.paginationConfig.pageIndex - 1) * this.paginationConfig.pageSize,
        limit: this.paginationConfig.pageSize,
        bulkFileID: this.specificDetail.fileID,
        validLevel: 0,
        isHis: 0,
      };
      if (this.routerType === 'process') {
        params.isHis = '0';
        params.finishTime = this.specificDetail.finishTime;
      }
      let res = await queryValidRecords(params);
      this.isSearch = false;
      if (res.header && res.header.success) {
        return {
          header: {
            success: 'success',
          },
          list: res.data.bulkDataInfoList || [],
          totalRecords: Number(res.data.numberOfTotal) || 0,
        };
      } else {
        return {
          header: {
            success: 'success',
          },
          list: [],
          totalRecords: 0,
        };
      }
    },
    async getDetailData() {
      let params = this.bulkPlanID;
      let res = await queryBulkSpecificInfo(params);
      if (res) {
        this.specificDetail = res.data.bulkPlanInfo.basicBulkPlanInfo;
        this.specificDetail = {
          bulkTypeName: res.data.bulkPlanInfo.basicBulkPlanInfo.bulkTypeName,
          planID: res.data.bulkPlanInfo.basicBulkPlanInfo.planID,
          planName: res.data.bulkPlanInfo.basicBulkPlanInfo.planName,
          bulkStatus: res.data.bulkPlanInfo.basicBulkPlanInfo.bulkStatus,
          createdBy: res.data.bulkPlanInfo.basicBulkPlanInfo.createdBy,
          createTime: res.data.bulkPlanInfo.basicBulkPlanInfo.createTime,
          scheduleTime: res.data.bulkPlanInfo.basicBulkPlanInfo.scheduleTime,
          dataFileName: res.data.bulkPlanInfo.basicBulkPlanInfo.dataFileName,
          numberOfTotal: res.data.bulkPlanInfo.basicBulkPlanInfo.bulkTypeName,
          numberOfValidSuccess: res.data.bulkPlanInfo.bulkResultInfo.numberOfValidSuccess,
          validAmount: res.data.bulkPlanInfo.bulkResultInfo.validAmount,
          numberOfValidFailed: res.data.bulkPlanInfo.bulkResultInfo.numberOfValidFailed,
          fileID: res.data.bulkPlanInfo.bulkResultInfo.fileID,
        };
      }
    },
    handleDownLoad() {
      downloadBulkFile(this.specificDetail.fileID, 1);
    },
    handleClick() {
      this.isSearch = true;
      this.dialogVisible = true;
    },
    handleDialogCancel() {
      this.dialogVisible = false;
    },
  },
  mounted() {},
};
</script>
<style scoped lang="scss">
@import '../assets/detail.scss';
.task-bulk-detail {
  .task-bulk-detail-content {
    .basic-info {
      padding-bottom: 1px;
      margin-top: 16px;
      width: 100%;
      background: $base-logo-title-color;
    }
  }
  /deep/ .el-divider--horizontal {
    margin: 0px !important;
    background-color: #f0f0f0 !important;
  }
}
</style>
