<template>
  <el-dialog
    :title="$t('IDC.orgOperator.editStatus')"
    :visible.sync="editStatusVisible"
    width="448px"
    :before-close="closeEdit"
  >
    <el-form label-position="top" :model="editForm" ref="editForm" :rules="editFormRules">
      <el-row :gutter="20">
        <el-col :span="24">
          <el-form-item :label="$t('IDC.orgOperator.currentStatus')">
            <div class="text">
              <span class="text">{{ activeType }}</span>
            </div>
          </el-form-item>
        </el-col>
        <el-col :span="24">
          <el-form-item :label="$t('IDC.orgOperator.newStatus')" prop="currentStatus">
            <el-select size="small" v-model="editForm.currentStatus" :placeholder="$t('common.placeholder.select')">
              <el-option :label="activeLabel" :value="active"></el-option>
            </el-select>
          </el-form-item>
        </el-col>

        <el-col :span="24" class="new-status">
          <el-form-item :label="$t('IDC.orgOperator.reason')" prop="reason">
            <el-select size="small" v-model="editForm.reason" :placeholder="$t('common.placeholder.select')">
              <el-option label="Not Use" value="notUse"></el-option>
              <el-option label="Id Expired" value="idExpired"></el-option>
            </el-select>
          </el-form-item>
        </el-col>

        <el-col :span="24">
          <el-form-item :label="$t('IDC.orgOperator.remark')" prop="remark">
            <el-input size="small" type="textarea" rows="4" v-model="editForm.remark"> </el-input>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>

    <span slot="footer" class="dialog-footer">
      <el-button @click="closeEdit" size="small">{{ $t('common.button.cancel') }}</el-button>
      <el-button type="primary" size="small" @click="editSubmit" v-preventReClick>{{
        $t('common.button.submit')
      }}</el-button>
    </span>
  </el-dialog>
</template>

<script>
import { changeStatus } from '../../../api/api-task-center';
export default {
  name: 'OrgEditStatus',
  props: {
    editStatusVisible: {
      type: Boolean,
      default: false,
    },
    editForm: {
      type: Object,
      default: () => ({}),
    },
    bizContext: {
      type: Object,
      default: () => ({}),
    },
    datalist: {
      type: Object,
      default: () => ({}),
    },
  },
  data() {
    return {
      editFormRules: {
        currentStatus: [{ required: true, Message: 'please select', trigger: 'change' }],
        reason: [],
        remark: [{ required: true, Message: 'please select', trigger: 'blur' }],
      },
      currentTasktype: '',
    };
  },
  computed: {
    activeType() {
      if (this.currentTasktype === '04') {
        return 'Suspended';
      } else {
        return 'Active';
      }
    },
    active() {
      if (this.currentTasktype === '04') {
        return '03';
      } else {
        return '04';
      }
    },
    activeLabel() {
      if (this.currentTasktype === '04') {
        return 'Active';
      } else {
        return 'Suspended';
      }
    },
  },
  methods: {
    closeEdit() {
      this.$emit('closeDialog');
    },

    async editSubmit() {
      let variables = JSON.parse(this.bizContext.variables.changeInfo);
      this.currentTasktype = variables.oldStatus;
      variables.oldStatus = this.currentTasktype;
      variables.newStatus = this.active;
      let params = {
        actionType: 'Approve',
        comments: this.editForm.remark,
        taskId: this.datalist.taskId,
        processId: this.datalist.processId,
        bizContext: { variables: { changeInfo: JSON.stringify(variables) } },
      };
      this.$refs.editForm.validate((valid) => {
        if (valid) {
          changeStatus(params).then((res) => {
            if (res.header && res.header.success) {
              this.closeEdit();
              this.$emit('change-status-succeed');
            }
          });
        }
      });
    },
  },
};
</script>

