<template>
  <div>
    <div class="basic-info-box">
      <el-row>
        <el-col v-bind="colOptions" v-for="(item, index) of configList" :key="index">
          <div class="basic-info-card">
            <div class="basic-info-card__label bg-purple">
              {{ item.label }}
            </div>
            <div class="basic-info-card__value">
              {{ item.value }}
              <template>
                <el-link
                  :underline="false"
                  v-if="newStatusIsShow(item)"
                  icon="el-icon-edit"
                  @click="showEditDialog"
                ></el-link>
              </template>
            </div>
          </div>
        </el-col>
      </el-row>
    </div>
  </div>
</template>
<script>
export default {
  name: 'BasicInfoBox',

  props: {
    basicInfoItemList: {
      type: Object,
      default: () => ({}),
    },
    datalist: {
      type: Object,
      default: () => ({}),
    },
  },

  data() {
    return {
      aa: true,
      propsOrder: [
        {
          name: 'oldStatus',
          label: this.$t('TAC.workflowDetail.oldStatus'),
        },
        {
          name: 'newStatus',
          label: this.$t('TAC.workflowDetail.newStatus'),
        },
        {
          name: 'reason',
          label: this.$t('TAC.workflowDetail.reason'),
        },
        {
          name: 'remark',
          label: this.$t('TAC.workflowDetail.remark'),
        },
      ],
    };
  },

  computed: {
    configList() {
      const configMap = this.basicInfoItemList;
      return this.propsOrder.map((field) => {
        const name = field.name;
        const label = field.label;
        return {
          label,
          value: configMap[name] || '-',
        };
      });
    },
  },
  methods: {
    showEditDialog() {
      this.$emit('show-change-status');
    },
    newStatusIsShow(val) {
      if (val.label === 'New Status' && this.datalist.taskType === 'Modify') {
        return true;
      }
    },
  },
};
</script>
<style scoped lang="scss">
.basic-info {
  font-weight: bold;
  font-size: 16px;
}
.el-row {
  .basic-info-card {
    padding-bottom: 20px;
    font-size: 14px;

    &__label {
      margin-bottom: 8px;
      color: $--text_color--secondary;
    }

    &__value {
      color: $--text_color--strong;
    }
  }
}
</style>
<style>
.el-icon-edit::before {
  padding-left: 12px;
}
</style>
