import { cloneDeep } from 'lodash';

const MULTI_GROUP_KEY_TO_ID_MAP = {
  idDetailList: 100, // ID Details
  certificatelList: 200, // Certificate Details
  contactList: 300, // Organization Contact Details
  directorDetailList: 500, // Director Details
  bankCardList: 1006, // Bank Detail
};

export const diffKYCConfigFormData = (newKYCData, oldKYCData) => {
  newKYCData = cloneDeep(newKYCData);
  const groupKey = Object.keys(MULTI_GROUP_KEY_TO_ID_MAP);
  const kycKey = Object.keys(oldKYCData || {});
  for (let key of kycKey) {
    if (groupKey.includes(key)) {
      newKYCData = formatKeyFn(newKYCData, oldKYCData, key);
    } else {
      oldKYCData &&
        oldKYCData[key].forEach((kycItem, index) => {
          kycItem &&
            kycItem.kycFields &&
            kycItem.kycFields.forEach((subKycItem, subIndex) => {
              if (
                newKYCData &&
                newKYCData[key] &&
                newKYCData[key][index] &&
                newKYCData[key][index].kycFields &&
                newKYCData[key][index].kycFields[subIndex] &&
                newKYCData[key][index].kycFields[subIndex].displayValue !== subKycItem.displayValue
              ) {
                const newField = subKycItem.displayValue ? 'isModifyed' : 'isNewlyIncreased';
                newKYCData[key][index].kycFields[subIndex][newField] = true;
              }
            });
        });
    }
  }

  return newKYCData;
};

function formatKeyFn(newKYCData, oldKYCData, key) {
  newKYCData &&
    newKYCData[key] &&
    newKYCData[key].forEach((newKycItem) => {
      const oldKycItem =
        oldKYCData[key] && oldKYCData[key].find((oldKycItem) => oldKycItem.recordId === newKycItem.recordId);
      if (oldKycItem) {
        newKycItem &&
          newKycItem.kycFields &&
          newKycItem.kycFields.forEach((subKycItem, subIndex) => {
            if (
              (subKycItem.displayValue || oldKycItem.kycFields[subIndex].displayValue) &&
              subKycItem.displayValue !== oldKycItem.kycFields[subIndex].displayValue
            ) {
              const newField =
                oldKycItem.kycFields && oldKycItem.kycFields[subIndex] && oldKycItem.kycFields[subIndex].displayValue
                  ? 'isModifyed'
                  : 'isNewlyIncreased';
              subKycItem[newField] = true;
            }
          });
      } else {
        newKycItem &&
          newKycItem.kycFields.forEach((subKycItem) => {
            subKycItem.isNewlyIncreased = true;
          });
      }
    });
  return newKYCData;
}

/**
 * 将kyc传递过来的数据转成实体所需要的数据
 */
export const transKYCDataToWorkflowConfig = (kycData, workflowConfig) => {
  workflowConfig = cloneDeep(workflowConfig);
  const groupKey = Object.keys(MULTI_GROUP_KEY_TO_ID_MAP);
  const configKeys = Object.keys(workflowConfig);
  for (let key of configKeys) {
    if (groupKey.includes(key)) {
      if (kycData[key] && kycData[key].length) {
        workflowConfig[key] = transMulitGroupQuest(workflowConfig[key], kycData[key]);
      } else {
        workflowConfig[key] = [];
      }
    } else {
      workflowConfig[key].forEach((config) => {
        const kycItemData = kycData[key].find((kyc) => kyc.kycFieldId === config.kycFieldId);
        config.inputValue = kycItemData.inputValue;
      });
    }
  }
  return workflowConfig;
};

function transMulitGroupQuest(workflowConfig, kycConfig) {
  const configData = cloneDeep(workflowConfig);
  const configTemp = cloneDeep(configData[0]);
  if (!configData) {
    return kycConfig;
  }
  delete configTemp.recordId;
  return kycConfig.map((item, index) => {
    item.kycFields = item.kycFields.map((subItem, sIndex) => {
      return Object.assign({}, configTemp.kycFields[sIndex], {
        inputValue: subItem.inputValue,
      });
    });
    const currentItem = configData.find(child => child.recordId == item.recordId);
    if (currentItem && currentItem.recordId) {
      currentItem.recordId = item.recordId;
    }
    // 新增的数据不传recordId，由于目前不涉及排序及现有数据删除，可以按顺序赋值recordId
    return Object.assign({}, currentItem || configTemp, { kycFields: item.kycFields });
  });
}
