<template>
  <div class="org-product">
    <div class="basic-info">
      <div class="basic-info-title">{{ $t('TAC.workflowDetail.changeInfo') }}</div>
    </div>
    <el-divider class="specific-table-divider"></el-divider>
    <div class="specific-product">
      <el-tabs type="border-card" @tab-click="handleTabClick">
        <el-tab-pane label="New Product"></el-tab-pane>
        <el-tab-pane label="Old Product" v-if="!isHistory"></el-tab-pane>
      </el-tabs>
      <div class="specific-product-container">
        <product-table
          class="workflow-product-table"
          :mode="productMode"
          :showBillValid="false"
          :isReceiveData="true"
          :identityId="identityId"
          :receiveProductData="productData"
          @product-submit="handleProductSubmit"
          @product-cancel="handleProductCancel"
        />
      </div>
    </div>
    <div class="specific-container">
      <specific-info size="small" :infoList="specificInfoList"></specific-info>
    </div>
  </div>
</template>

<script>
import SpecificInfo from '@/views/task-center/component/specific-info';
import ProductTable from '@/views/identifier-center/pages/organization/detail/components/product-info';
import { querySubscribeProductsForWorkFlow } from '@/views/task-center/api/api-task-center';
import { querySubscribeProducts } from '@/views/identifier-center/api/api-organization';
import { cloneDeep } from 'lodash';

export default {
  name: 'OrginazationProduct',
  props: {
    bizContext: {
      type: Object,
      default: () => {},
    },
    isEdit: {
      type: Boolean,
      default: false,
    },
    isHistory: {
      type: Boolean,
      default: false,
    },
  },
  components: {
    SpecificInfo,
    ProductTable,
  },
  provide() {
    return {
      orgDetailView: {},
    };
  },
  data() {
    return {
      tabIndex: '0',
      oldProductData: [],
      newProductData: [],
      specificDetail: {},
    };
  },
  computed: {
    specificInfoList() {
      let remark = '';
      try {
        remark = this.bizContext.variables.changeInfo.remark;
      } catch {
        remark = '';
      }
      return [
        {
          label: 'remark',
          value: remark,
        },
      ];
    },
    productMode() {
      return this.isEdit && this.tabIndex === '0' ? 'edit' : 'review';
    },
    initProductData() {
      let productData = [];
      try {
        productData = this.bizContext.variables.changeInfo.newOrganizationProducts;
      } catch {
        productData = [];
      }
      return productData;
    },
    identityId() {
      let identityId = '';
      try {
        identityId = this.bizContext.variables.changeInfo.receiverIdentityId;
      } catch {
        identityId = '';
      }
      return identityId || '';
    },
    productData() {
      return this.tabIndex === '0' ? this.newProductData : this.oldProductData;
    },
  },
  watch: {
    initProductData: {
      handler(val) {
        if (val && val.length) {
          this.getNewProductData(val);
        }
      },
      deep: true,
    },
    identityId(val) {
      if (val) {
        this.getOldProductData(val);
      }
    },
  },
  methods: {
    handleTabClick(tab) {
      this.tabIndex = tab.index;
    },
    getNewProductData(orgSubscribeProductDTOS) {
      querySubscribeProductsForWorkFlow({ orgSubscribeProductDTOS }).then((res) => {
        if (res.header && res.header.success) {
          this.newProductData = res.list || [];
        }
      });
    },
    getOldProductData(identityId) {
      querySubscribeProducts({ identityId }).then((res) => {
        if (res.header && res.header.success) {
          this.oldProductData = res.list || [];
        }
      });
    },
    handleProductSubmit(data) {
      const bizContext = {};
      try {
        bizContext.variables = cloneDeep(this.bizContext.variables);
      } catch {
        bizContext.variables = {};
      }
      bizContext.variables.changeInfo.newOrganizationProducts = data.products.map((item) => {
        item.effectiveTime = item.effectiveTime.replace(/\s/, 'T');
        item.expireTime = item.expireTime.replace(/\s/, 'T');
        return item;
      });
      bizContext.variables.changeInfo = JSON.stringify(bizContext.variables.changeInfo);
      const params = {
        bizContext,
        remark: data.remark,
      };
      this.$emit('org-submit', params);
    },
    handleProductCancel() {},
  },
};
</script>

