<template>
  <div class="specific-info-container">
    <div v-if="title" class="specific-title">{{ title }}</div>
    <el-form label-position="top">
      <el-row :gutter="20" type="flex" class="el-flex-wrap">
        <el-col v-for="(item, index) in infoList" :key="index" v-bind="item.col || colOptions">
          <el-form-item :label="$t('TAC.specificInfo.' + item.label)">
            <template v-if="item.slot">
              <slot :name="item.slot" :data="item"></slot>
            </template>
            <template v-else>
              <span v-if="!item.isClick">{{ item.value || '-' }}</span>
              <span v-if="item.isClick" class="click-color" @click="handleClick(item)">{{ item.value || '-' }}</span>
              <span v-if="item.isDownLoad" class="down-load" @click="downLoad(item)">Download</span>
            </template>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
  </div>
</template>

<script>
export default {
  name: 'specificInfoItem',
  props: {
    infoList: {
      type: Array,
      default: () => [],
    },
    title: {
      type: String,
      default: '',
    },
  },
  data() {
    return {};
  },
  methods: {
    downLoad(item) {
      this.$emit('item-down-load', item);
    },
    handleClick(item) {
      this.$emit('item-click', item);
    },
  },
};
</script>

