<template>
  <div class="specific-info">
    <div class="basic-info-title specific-title">
      <span>{{ $t('TAC.workflowDetail.specificInfo') }}</span>
    </div>
    <el-divider class="specific-table-divider"></el-divider>
    <div class="specific-container">
      <customer-info :identityID="receiverIdentityId" cpId=""></customer-info>
    </div>
    <div class="basic-info">
      <div class="basic-info-title">{{ $t('TAC.workflowDetail.changeInfo') }}</div>
      <!-- <el-divider class="table-divider"></el-divider> -->
      <div class="specific-content">
        <el-tabs v-model="activeName">
          <el-tab-pane label="New KYC" name="first"> </el-tab-pane>
          <el-tab-pane label="Old KYC" name="second" v-if="!isHistory"> </el-tab-pane>
        </el-tabs>
        <div class="kyc-container">
          <kyc-form
            :mode="kycMode"
            :incomingData="reviewKycData"
            :identityId="receiverIdentityId"
            identityClassify="customer"
            @edit-submit-success="submitSuccess"
            @workflow-kyc-cancel="handleKYCCancel"
            @workflow-kyc-submit="handleKYCSubmit"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import CustomerInfo from '../components/customer-info.vue';
import KycForm from '@/components/kyc-common/kyc-form/index.vue';
import { diffKYCConfigFormData, transKYCDataToWorkflowConfig } from '../utils/util';
import { queryBackKycToInitiator } from '@/views/task-center/api/api-task-center';
import { getCustomerKYCDetail } from '@/components/kyc-common/kyc-form/kyc-api.js';
import { cloneDeep } from 'lodash';

export default {
  name: 'OrganizationKyc',
  props: {
    receiverIdentityId: {
      type: String,
      default: '',
    },
    newKycFormData: {
      type: Object,
      default: () => {},
    },
    bizContext: {
      type: Object,
      default: () => {},
    },
    taskDetail: {
      type: Object,
      default: () => {},
    },
    isEdit: {
      type: Boolean,
      default: false,
    },
    isHistory: {
      type: Boolean,
      default: false,
    },
  },
  components: {
    CustomerInfo,
    KycForm,
  },
  data() {
    return {
      activeName: 'first',
      specificDetail: {},
      newKycForm: {},
      oldKycForm: {},
    };
  },
  computed: {
    orgSpecificInfoList() {
      return [
        {
          label: 'organizationName',
          value: this.specificDetail.organizationName,
        },
        {
          label: 'shortCode',
          value: this.specificDetail.shortCode,
        },
        {
          label: 'organizationCode',
          value: this.specificDetail.organizationCode,
        },
        {
          label: 'identityStatus',
          value: this.specificDetail.status,
        },
        {
          label: 'segment',
          value: this.specificDetail.segment,
        },
        {
          label: 'chargeProfile',
          value: this.specificDetail.chargeProfile,
        },
        {
          label: 'ruleProfile',
          value: this.specificDetail.ruleProfileName,
        },
        {
          label: 'trustLevel',
          value: this.specificDetail.trustLevelName,
        },
        {
          label: 'registrationDate',
          value: dayjs(this.specificDetail.createTime).format('DD/MM/YYYY HH:mm:ss'),
        },
      ];
    },
    reviewKycData() {
      return {
        diffKYCData: this.activeName === 'first' ? this.newKycForm : this.oldKycForm,
      };
    },
    kycMode() {
      return this.isEdit && this.activeName === 'first' ? 'workflowEdit' : 'workflowDiff';
    },
  },
  watch: {
    receiverIdentityId(val) {
      if (val && !this.isHistory) {
        this.getOldKYCDetail(val);
      }
    },
    newKycFormData: {
      handler(val) {
        this.newKycForm = val;
      },
      deep: true,
    },
  },
  methods: {
    submitSuccess() {
      this.$store.dispatch('tagsView/delView', this.$route);
      this.$router.push('list');
    },
    handleKYCCancel() {
      const kycFormConfig = this.newKycForm;
      this.newKycForm = {};
      this.$nextTick(() => {
        this.newKycForm = kycFormConfig;
      });
    },
    getOldKYCDetail(customerId) {
      getCustomerKYCDetail({ identityId: customerId })
        .then((res) => {
          const { header, identityKYCDataBean } = res;
          if (header && header.success) {
            this.oldKycForm = identityKYCDataBean;
            this.newKycForm = diffKYCConfigFormData(this.newKycForm, this.oldKycForm);
          }
        })
        .catch(() => {});
    },
    handleKYCSubmit(KYCData, remark) {
      const bizContext = cloneDeep(this.bizContext);
      bizContext.variables.changeInfo = JSON.parse(bizContext.variables.changeInfo);
      bizContext.variables.changeInfo.identityKYCData = transKYCDataToWorkflowConfig(
        KYCData,
        bizContext.variables.changeInfo.identityKYCData
      );
      bizContext.variables.changeInfo = JSON.stringify(bizContext.variables.changeInfo);
      const params = {
        actionType: 'Approve',
        initiatorId: this.taskDetail.initiatorId,
        processId: this.taskDetail.processId,
        taskId: this.taskDetail.taskId,
        comments: remark,
        bizContext,
      };
      queryBackKycToInitiator(params).then((res) => {
        if (res.header && res.header.success) {
          this.$message.success('Submit ' + this.$t('TAC.tips.taskSuccess'));
          this.$router.push('list');
        } else {
          this.$message.error('Submit ' + this.$t('TAC.tips.taskFailed'));
        }
      });
    },
  },
};
</script>

<style lang="scss" scoped>
@import '../assets/detail.scss';
</style>
