  <div class="customer-detail-info-page">
    <el-form :model="basicInfoForm" ref="basicInfoForm" label-position="top" size="small">
      <div class="basic-info-container">
        <div class="specific-content-title">{{ $t('TAC.workflowDetail.basicInfo') }}</div>
        <el-row :gutter="20">
          <el-col :span="8">
            <el-form-item :label="$t('IDC.orgCustomer.customerType')">
              {{ showCustomerType(basicInfoForm.customerType) || '-' }}
            </el-form-item>
          </el-col>
          <el-col :span="8">
