          <el-divider />
          <div class="section-title">{{ $t('IDC.orgCustomer.verifyInfo') }}</div>
          <el-row :gutter="40">
            <el-col :span="8">
              <el-form-item :label="$t('IDC.orgCustomer.verifyStatus')">
                {{ (basicInfoForm && basicInfoForm.verifyInfoDTO && basicInfoForm.verifyInfoDTO.verifyStatus) || '-' }}
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item :label="$t('IDC.orgCustomer.startTime')">
