<script>
import { queryCustomerInfo, queryLanguage } from '../../../../identifier-center/api/api-customer';
import dayjs from 'dayjs';
export default {
  name: 'CustomerInfo',
  props: {
    isShowVerify: {
      type: Boolean,
      default: false,
    },
    languageName: {
      type: String,
      default: '',
    },
    identityID: {
      type: String,
      default: '',
    },
    cpId: {
      type: String,
      default: '',
    },
  },
  data() {
    return {
      titleConfig: {
        title: '',
        desc: this.$t('IDC.groupDetail.pageDesc'),
      },
      basicInfoForm: {},
      dialogType: '',
    };
  },
  created() {},
  watch: {
    identityID(val) {
      if (val) {
        this.getCustomerInfoFn();
      }
    },
  },
  activated() {},
  deactivated() {
    this.basicInfoForm = {};
  },
  computed: {},
  methods: {
    handleKYCCancel() {},
    handleKYCSubmit() {},
    showCustomerType(val) {
      switch (val) {
        case '1002':
          return this.$t('IDC.orgCustomer.registeredCustomer');
        case '1001':
          return this.$t('IDC.orgCustomer.liteCustomer');
        case '1000':
          return this.$t('IDC.orgCustomer.unregisteredCustomer');
        default:
          return;
      }
    },
    showCustomerStatus(val) {
      switch (val) {
        case '1002':
          return this.$t('IDC.orgCustomer.registeredCustomer');
        case '1001':
          return this.$t('IDC.orgCustomer.liteCustomer');
        case '1000':
          return this.$t('IDC.orgCustomer.unregisteredCustomer');
        default:
          return;
      }
    },
    getCustomerInfoFn() {
      return new Promise((resolve) => {
        // 入参 {customerId: "1011001100000089815", cpId: "1001"}
        let params = {};
        // 注意：临时代码
        params.cpId = this.cpId;
        params.customerId = this.identityID;
        queryCustomerInfo(params)
          .then((res) => {
            if (res.header && res.header.success) {
              res.totalRecords = res.total;
            }
            this.basicInfoForm = { ...res };
            this.basicInfoForm.registerTime = dayjs(this.basicInfoForm.registerTime).format('DD/MM/YYYY');
            this.datilStatus = this.showStatus(this.basicInfoForm.status);
            // 只有Suspended 04状态会有这块Verify Info字段显示。
            if (this.basicInfoForm.status === '05') {
              this.isShowVerify = true;
            }
            // 语言接口
            queryLanguage(params)
              .then((re) => {
                if (re.header && re.header.success) {
                  let lang;
                  lang = re.locales;
                  for (let key in lang) {
                    if (lang[key].id === this.basicInfoForm.customerBasicKyc.languageCode) {
                      this.languageName = lang[key].name;
                    }
                  }
                }
                resolve(re);
              })
              .catch(() => {})
              .finally(() => {});
            resolve(res);
          })
          .catch(() => {})
          .finally(() => {
            this.refreshTableFlag = false;
            this.searchLoading = false;
          });
      });
    },
  },
};
</script>

