<template>
  <div class="org-status">
    <div class="basic-info">
      <div class="basic-info-title">{{ $t('TAC.workflowDetail.changeInfo') }}</div>
    </div>
    <el-divider class="specific-table-divider"></el-divider>
    <div class="specific-container">
      <specific-info :infoList="specificInfoList">
        <template slot="new-status" slot-scope="scope">
          <el-link
            v-if="isEdit"
            :underline="false"
            icon="el-icon-edit"
            @click="handleShowEdit"
            >
            {{ scope.data.value || '-' }}
          </el-link>
          <span v-else>{{ scope.data.value || '-' }}</span>
        </template>
      </specific-info>
    </div>

    <content-item-edit-dialog
      :visible.sync="editVisible"
      :config="dialogConfig"
      :submitLoading="submitLoading"
      :close-on-click-modal="false"
      @on-submit="handleDialogSubmit"
    />
  </div>
</template>

<script>
import SpecificInfo from '@/views/task-center/component/specific-info';
import ContentItemEditDialog from '@/views/identifier-center/pages/organization/detail/components/content-item-edit-dialog';
import { getOrganizationStatus } from '@/views/identifier-center/api/api-organization';
import { getIdentityStatusName } from '@/views/identifier-center/pages/organization/utils/organizatoin-enums';
import { STATUS_MAP } from '../../utils/data-dic.js';
import { cloneDeep } from 'lodash';

export default {
  name: 'OrganizationStatus',
  props: {
    bizContext: {
      type: Object,
      default: () => {},
    },
    isEdit: {
      type: Boolean,
      default: false,
    },
  },
  components: {
    SpecificInfo,
    ContentItemEditDialog
  },
  data () {
    return {
      editVisible: false,
      submitLoading: false,
      statusOpts: []
    }
  },
  computed: {
    specificDetail() {
      let specificDetail;
      try {
        const { oldStatus, newStatus, reaason, remark } = this.bizContext.variables.changeInfo;
        specificDetail = { oldStatus, newStatus, reaason, remark };
      } catch {
        specificDetail = {};
      }
      return specificDetail;
    },
    specificInfoList() {
      return [
        {
          label: 'oldStatus',
          value: STATUS_MAP[this.specificDetail.oldStatus],
        },
        {
          label: 'newStatus',
          value: STATUS_MAP[this.specificDetail.newStatus],
          slot: 'new-status'
        },
        {
          label: 'reason',
          value: this.specificDetail.reason,
        },
        {
          label: 'remark',
          value: this.specificDetail.remark,
        },
      ]
    },
    dialogConfig() {
      return {
        title: this.$t('IDC.orgOperator.editStatus'),
        label: this.$t('IDC.orgOperator.currentStatus'),
        value: this.getIdentityStatusName(this.specificDetail.newStatus),
        prop: 'identityStatus',
        properties: [
          {
            key: 'identityStatus',
            default: this.specificDetail.status,
            label: this.$t('IDC.orgOperator.newStatus'),
            type: 'select',
            options: this.statusOpts,
          },
          {
            key: 'remark',
            default: '',
            label: this.$t('common.label.remark'),
            type: 'textarea',
          },
        ],
      };
    }
  },
  watch: {},
  methods: {
    getIdentityStatusName(status) {
      return getIdentityStatusName(status);
    },
    async loadOrganizationStatus(status) {
      const res = await getOrganizationStatus({ statusCode: status });
      let data = res.statusList;
      if (data && data.length) {
        this.statusOpts = [];
        this.statusOpts.push(
          ...data.map((e) => {
            return {
              value: e.code,
              label: e.name,
            };
          })
        );
      }
    },
    handleShowEdit() {
      this.loadOrganizationStatus(this.specificDetail.newStatus).then(() => {
        this.editVisible = true;
      })
    },
    handleDialogSubmit(type, data) {
      console.log(type, data)
      const bizContext = {};
      bizContext.variables = cloneDeep(this.bizContext.variables);
      bizContext.variables.changeInfo.organizationName = data.newOrganizationName;
      bizContext.variables.changeInfo.remark = data.remark;
      bizContext.variables.changeInfo = JSON.stringify(bizContext.variables.changeInfo);
      const params = {
        bizContext,
        remark: data.remark,
      }
      this.$emit('org-submit', params)
    }
  },
}
</script>

<style lang='scss' scoped>
@import '../../assets/detail.scss';
</style>
