<template>
  <div class="specific-info">
    <div class="basic-info-title specific-title">
      <span>{{ $t('TAC.workflowDetail.specificInfo') }}</span>
    </div>
    <el-divider class="specific-table-divider"></el-divider>
    <div class="specific-container">
      <specific-info :infoList="specificInfoList"></specific-info>
    </div>

    <components
      ref="orgComponentRef"
      :is="orgComponent"
      :bizContext="orgBizContext"
      :isEdit="isEdit"
      :detailInfo="specificDetail"
      @org-submit="handleOrgSubmit"
    />
  </div>
</template>

<script>
import SpecificInfo from '@/views/task-center/component/specific-info';
import OrgProduct from './components/org-product.vue';
import OrgName from './components/org-name.vue';
import OrgStatus from './components/org-status';
import { queryBackToInitiator } from '@/views/task-center/api/api-task-center';
import { getOrganizationDetail } from '@/views/identifier-center/api/api-organization.js';
import { STATUS_MAP } from '../utils/data-dic.js';
import dayjs from 'dayjs';
import { cloneDeep } from 'lodash';

const ORG_COMPONENT_MAP = {
  approvalChangeOrgProduct: 'OrgProduct',
  approvalChangeOrgName: 'OrgName',
  approvalChangeOrgStatus: 'OrgStatus',
  // approvalChangeOrgAcctStatus: 'OrgAccountStatus', org暂没有此需求
};
export default {
  name: 'OrganizationTemp',
  props: {
    receiverIdentityId: {
      type: String,
      default: '',
    },
    bizContext: {
      type: Object,
      default: () => {},
    },
    taskDetail: {
      type: Object,
      default: () => {},
    },
    isEdit: {
      type: Boolean,
      default: false,
    },
    BMEBusiness: {
      type: String,
      default: '',
    },
  },
  components: {
    SpecificInfo,
    OrgProduct,
    OrgName,
    OrgStatus,
  },
  data() {
    return {
      specificDetail: {},
    };
  },
  computed: {
    orgComponent() {
      return ORG_COMPONENT_MAP[this.BMEBusiness];
    },
    specificInfoList() {
      return [
        {
          label: 'organizationName',
          value: this.specificDetail.organizationName,
        },
        {
          label: 'shortCode',
          value: this.specificDetail.shortCode,
        },
        {
          label: 'organizationCode',
          value: this.specificDetail.organizationCode,
        },
        {
          label: 'identityStatus',
          value: STATUS_MAP[this.specificDetail.status],
        },
        {
          label: 'segment',
          value: this.specificDetail.segment,
        },
        {
          label: 'chargeProfile',
          value: this.specificDetail.chargeProfileName,
        },
        {
          label: 'ruleProfile',
          value: this.specificDetail.ruleProfileName,
        },
        {
          label: 'trustLevel',
          value: this.specificDetail.trustLevelName,
        },
        {
          label: 'registrationData',
          value: dayjs(this.specificDetail.createTime).format('DD/MM/YYYY HH:mm:ss'),
        },
      ];
    },
    orgBizContext() {
      let bizContext = cloneDeep(this.bizContext),
        changeInfo = {};
      try {
        changeInfo = JSON.parse(this.bizContext.variables.changeInfo);
        bizContext.variables.changeInfo = changeInfo;
      } catch {
        changeInfo = {};
      }
      return bizContext;
    },
  },
  watch: {
    receiverIdentityId(value) {
      if (value) {
        this.queryOrganizationDetail(value);
      }
    },
  },
  methods: {
    queryOrganizationDetail(identityId) {
      getOrganizationDetail({ identityId }).then((res) => {
        if (res.header && res.header.success) {
          this.specificDetail = res.organizationInfo;
        }
      });
    },
    handleOrgSubmit(data) {
      const params = {
        actionType: 'Approve',
        initiatorId: this.taskDetail.initiatorId,
        processId: this.taskDetail.processId,
        taskId: this.taskDetail.taskId,
        comments: data.remark,
        bizContext: data.bizContext,
      };
      this.$refs.orgComponentRef.submitLoading = true;
      queryBackToInitiator(params)
        .then((res) => {
          if (res.header && res.header.success) {
            this.$message.success('Submit ' + this.$t('TAC.tips.taskSuccess'));
            this.$router.push('list');
          } else {
            this.$message.error('Submit ' + this.$t('TAC.tips.taskFailed'));
          }
        })
        .finally(() => {
          this.$refs.orgComponentRef.submitLoading = false;
          this.$refs.orgComponentRef.editVisible = false;
        });
    },
  },
};
</script>

<style lang="scss" scoped>
@import '../assets/detail.scss';
</style>
