<template>
  <div class="specific-info">
    <div class="basic-info-title specific-title">
      <span>{{ $t('TAC.workflowDetail.specificInfo') }}</span>
    </div>
    <el-divider class="specific-table-divider"></el-divider>
    <div class="specific-container">
      <specific-info :title="operatorTitle" :infoList="operatorSpecificInfoList"></specific-info>
    </div>
    <div class="line"></div>
    <div class="basic-info">
      <div class="basic-info-title">{{ $t('TAC.workflowDetail.changeInfo') }}</div>
      <!-- <el-divider class="table-divider"></el-divider> -->
      <div class="specific-content">
        <el-tabs v-model="activeName">
          <el-tab-pane label="New KYC" name="first"> </el-tab-pane>
          <el-tab-pane label="Old KYC" name="second" v-if="!isHistory"> </el-tab-pane>
        </el-tabs>
        <div class="kyc-container">
          <kyc-form
            :mode="kycMode"
            :incomingData="reviewKycData"
            :identityId="receiverIdentityId"
            identityClassify="orgOper"
            @edit-submit-success="submitSuccess"
            @workflow-kyc-cancel="handleKYCCancel"
            @workflow-kyc-submit="handleKYCSubmit"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SpecificInfo from '@/views/task-center/component/specific-info';
import KycForm from '@/components/kyc-common/kyc-form/index.vue';
import { diffKYCConfigFormData, transKYCDataToWorkflowConfig } from '../utils/util';
import { queryLanguageList, queryBackKycToInitiator } from '@/views/task-center/api/api-task-center';
import { getOrgOperatorKYCDetail } from '@/components/kyc-common/kyc-form/kyc-api.js';
import { orgOperatorDetail } from '@/views/identifier-center/api/api-organization-operator.js';
import { cloneDeep } from 'lodash';

export default {
  name: 'OrgOperatorKyc',
  props: {
    receiverIdentityId: {
      type: String,
      default: '',
    },
    newKycFormData: {
      type: Object,
      default: () => {},
    },
    bizContext: {
      type: Object,
      default: () => {},
    },
    taskDetail: {
      type: Object,
      default: () => {},
    },
    isEdit: {
      type: Boolean,
      default: false,
    },
    isHistory: {
      type: Boolean,
      default: false,
    },
  },
  components: {
    SpecificInfo,
    KycForm,
  },
  data() {
    return {
      operatorTitle: this.$t('TAC.specificInfo.basicInfo'),
      operatorSpecificInfoList: [
        {
          label: 'identityId',
          key: 'orgOperatorId',
          value: '',
        },
        {
          label: 'identityStatus',
          key: 'status',
          value: '',
        },
        {
          label: 'orgShortCode',
          key: 'shortCode',
          value: '',
        },
        {
          label: 'userName',
          key: 'operatorName',
          value: '',
        },
        {
          label: 'operatorId',
          key: 'operatorId',
          value: '',
        },
        {
          label: 'accessChannel',
          key: 'accessChannel',
          value: '',
        },
        {
          label: 'language',
          key: 'language',
          value: '',
        },
        {
          label: 'ruleProfile',
          key: 'ruleProfile',
          value: '',
        },
        {
          label: 'msisdn',
          key: 'operatorMsisdn',
          value: '',
        },
        {
          label: 'individualLimit',
        },
        {
          label: 'registrationChannel',
        },
      ],
      activeName: 'first',
      specificDetail: {},
      languageList: [],
      newKycForm: {},
      oldKycForm: {},
    };
  },
  computed: {
    reviewKycData() {
      return {
        diffKYCData: this.activeName === 'first' ? this.newKycForm : this.oldKycForm,
      };
    },
    kycMode() {
      return this.isEdit && this.activeName === 'first' ? 'workflowEdit' : 'workflowDiff';
    },
  },
  watch: {
    receiverIdentityId(val) {
      if (val) {
        this.queryOperatorDetail();
        !this.isHistory && this.getOldKYCDetail(val);
      }
    },
    newKycFormData: {
      handler(val) {
        this.newKycForm = val;
      },
      deep: true,
    },
  },
  methods: {
    submitSuccess() {
      this.$store.dispatch('tagsView/delView', this.$route);
      this.$router.push('list');
    },
    async queryOperatorDetail() {
      let res = await orgOperatorDetail({ receiverIdentityId: this.receiverIdentityId });
      if (res.header && res.header.success) {
        this.operatorSpecificInfoList.forEach((item, idx) => {
          if (item.label === 'identityStatus') {
            let val = this.getConverseStatus(res.orgOperatorInfo.status);
            this.operatorSpecificInfoList[idx].value = val;
          } else if (item.label === 'language') {
            let _idx = idx;
            queryLanguageList().then((resolve) => {
              if (resolve.header && resolve.header.success) {
                resolve.list.some((lItem) => {
                  if (lItem.languageCode === res.orgOperatorInfo.language) {
                    this.operatorSpecificInfoList[_idx].value = lItem.languageName;
                    return true;
                  }
                });
              }
            });
          } else if (item.label === 'accessChannel') {
            let val = '';
            [
              { value: '1005', label: 'Web' },
              { value: '1007', label: 'Handset' },
              { value: '1010', label: 'API' },
            ].forEach((aItem) => {
              res.orgOperatorInfo.accessChannel.forEach((cItem) => {
                if (aItem.value === cItem) {
                  !val ? (val = aItem.label) : (val = val + ',' + aItem.label);
                }
              });
            });
            this.operatorSpecificInfoList[idx].value = val;
          } else {
            this.operatorSpecificInfoList[idx].value = res.orgOperatorInfo[item.key];
          }
        });
      }
    },
    getConverseStatus(val) {
      switch (val) {
        case '03':
          return 'Active';
        case '06':
          return 'Closed';
        case '07':
          return 'Capped';
        case '00':
          return 'New';
        case '02':
          return 'Pending Active';
        case '04':
          return 'Suspended';
        case '05':
          return 'Frozen';
        case '08':
          return 'Dormant';
        default:
          return;
      }
    },
    getOldKYCDetail(customerId) {
      getOrgOperatorKYCDetail({ identityId: customerId })
        .then((res) => {
          const { header, identityKYCDataBean } = res;
          if (header && header.success) {
            this.oldKycForm = identityKYCDataBean;
            this.newKycForm = diffKYCConfigFormData(this.newKycForm, this.oldKycForm);
          }
        })
        .catch(() => {});
    },
    handleKYCCancel() {
      const kycFormConfig = this.newKycForm;
      this.newKycForm = {};
      this.$nextTick(() => {
        this.newKycForm = kycFormConfig;
      });
    },
    handleKYCSubmit(KYCData, remark) {
      const bizContext = cloneDeep(this.bizContext);
      bizContext.variables.changeInfo = JSON.parse(bizContext.variables.changeInfo);
      bizContext.variables.changeInfo.identityKYCData = transKYCDataToWorkflowConfig(
        KYCData,
        bizContext.variables.changeInfo.identityKYCData
      );
      bizContext.variables.changeInfo = JSON.stringify(bizContext.variables.changeInfo);
      const params = {
        actionType: 'Approve',
        initiatorId: this.taskDetail.initiatorId,
        processId: this.taskDetail.processId,
        taskId: this.taskDetail.taskId,
        comments: remark,
        bizContext,
      };
      queryBackKycToInitiator(params).then((res) => {
        if (res.header && res.header.success) {
          this.$message.success('Submit ' + this.$t('TAC.tips.taskSuccess'));
          this.$router.push('list');
        } else {
          this.$message.error('Submit ' + this.$t('TAC.tips.taskFailed'));
        }
      });
    },
  },
};
</script>

<style lang="scss" scoped>
@import '../assets/detail.scss';
</style>
