<template>
  <div class="org-name">
    <div class="basic-info">
      <div class="basic-info-title">{{ $t('TAC.workflowDetail.changeInfo') }}</div>
    </div>
    <el-divider class="specific-table-divider"></el-divider>
    <div class="specific-container">
      <specific-info :infoList="specificInfoList">
        <template slot="organization-name">
          <el-link
            v-if="isEdit"
            :underline="false"
            icon="el-icon-edit"
            @click="handleShowEdit"
            >
            {{ specificDetail.organizationName || '-' }}
          </el-link>
          <span v-else>{{ specificDetail.organizationName || '-' }}</span>
        </template>
      </specific-info>
    </div>

    <content-item-edit-dialog
      :visible.sync="editVisible"
      :config="dialogConfig"
      :submitLoading="submitLoading"
      :close-on-click-modal="false"
      @on-submit="handleDialogSubmit"
    />
  </div>
</template>

<script>
import SpecificInfo from '@/views/task-center/component/specific-info';
import ContentItemEditDialog from '@/views/identifier-center/pages/organization/detail/components/content-item-edit-dialog';
import { BasicInfoRule } from '@/views/identifier-center/validator/organization-info';
import { cloneDeep } from 'lodash';

export default {
  name: 'OrganizationName',
  props: {
    bizContext: {
      type: Object,
      default: () => {},
    },
    detailInfo: {
      type: Object,
      default: () => {}
    },
    isEdit: {
      type: Boolean,
      default: false,
    },
  },
  components: {
    SpecificInfo,
    ContentItemEditDialog
  },
  data () {
    return {
      editVisible: false,
      submitLoading: false,
      organizationRules: {}
    }
  },
  computed: {
    specificDetail() {
      let specificDetail;
      try {
        const { organizationName, remark } = this.bizContext.variables.changeInfo;
        specificDetail = { organizationName, remark };
      } catch {
        specificDetail = {};
      }
      return specificDetail;
    },
    specificInfoList() {
      return [
        {
          label: 'organizationName',
          value: this.specificDetail.organizationName,
          slot: 'organization-name',
        },
        {
          label: 'shortCode',
          value: this.detailInfo.shortCode,
        },
        {
          label: 'remark',
          value: this.specificDetail.remark,
        },
      ]
    },
    dialogConfig(){
      return {
        title: this.$t('IDC.organization.editOrganizationName'),
        label: 'Current Organization Name',
        value: this.specificDetail.organizationName,
        prop: 'organizationName',
        newOrganizationName: '',
        properties: [
          {
            key: 'newOrganizationName',
            default: '',
            label: this.$t('IDC.organization.newOrganizationName'),
            type: 'input',
            validateStatus: 'el-form-item-pending',
            itemClass: 'el-form-item-no-error',
            eleClass: 'success-icon',
            elementBind: {
              'suffix-icon': 'el-icon-success',
            },
          },
          {
            key: 'remark',
            default: '',
            label: this.$t('common.label.remark'),
            type: 'textarea',
          },
        ],  
      }
    }
  },
  watch: {},
  methods: {
    handleShowEdit() {
      this.editVisible = true;
    },
    handleDialogSubmit(type, data) {
      console.log(type, data)
      const bizContext = {};
      bizContext.variables = cloneDeep(this.bizContext.variables);
      bizContext.variables.changeInfo.organizationName = data.newOrganizationName;
      bizContext.variables.changeInfo.remark = data.remark;
      bizContext.variables.changeInfo = JSON.stringify(bizContext.variables.changeInfo);
      const params = {
        bizContext,
        remark: data.remark,
      }
      this.$emit('org-submit', params)
    }
  },
  created() {
    this.organizationRules = BasicInfoRule();
  }
}
</script>

<style lang='scss' scoped>
@import '../../assets/detail.scss';
</style>
